<template>
  <div id="firstall">
    <div id="protect" v-if="animatefalse">
      正在加载中...
      </div>
    <div id="firsttop">
      <div id="firstchartfirst">
        <canvas ref="chartCanvas"></canvas>
      </div>
      <ul>
        <li :class="{'animate__fadeInLeft animate__animated animate__bounce animate__delay-0.2s':animatefalse}">
          <img src="/src/3.png" alt="" class="firsttoppic">
          <p>今日使用了{{selfhistory[0]}}次情绪识别</p>
        </li>
        <li :class="{'animate__fadeInRight animate__animated animate__bounce animate__delay-0.4s':animatefalse}">
          <img src="/src/4.png" alt="" class="firsttoppic">
          <p>今日使用了{{selfhistory[1]}}次情绪调频</p>
        </li>
        <li :class="{'animate__fadeInUp animate__animated animate__bounce animate__delay-0.6s':animatefalse}">
          <img src="/src/5.png" alt="" class="firsttoppic">
          <p>今日使用了{{selfhistory[2]}}次音乐疗愈</p>
        </li>
        <li :class="{'animate__fadeInDown animate__animated animate__bounce animate__delay-0.8s':animatefalse}">
          <img src="/src/6.png" alt="" class="firsttoppic">
          <p>今日记录了{{selfhistory[3]}}次情绪日志</p>
        </li>
      </ul>
    </div>
    <div id="firstchartsecond">
      <div id="firstpiechart">
        <canvas ref="chartPie"></canvas>
      </div>
      <div id="todolist">
        <todolist/>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, nextTick, reactive } from 'vue';
import Chart from 'chart.js/auto';
import 'animate.css'
import todolist from '../todolist/todolist.vue';
export default {
  components:{
    todolist
  },
  props:['alldata'],
  setup(props) {
    const today=new Date()
    var animatefalse=ref(true)
    setTimeout(() => {
      animatefalse.value=false
    }, 1050);
    const chartCanvas = ref(null);
    const chartPie = ref(null);

    let firstchartfirst = null;
    let firstchartpie = null;
    var selfindicate=reactive(props.alldata[today.getFullYear()][today.getMonth()][today.getDate()].indicator)
    var selfhistory=reactive(props.alldata[today.getFullYear()][today.getMonth()][today.getDate()].history)
    var selfmood=reactive(props.alldata[today.getFullYear()][today.getMonth()][today.getDate()].mood)

    onMounted(async () => {
      await nextTick();

      // --- 第一个柱状图 ---
      if (chartCanvas.value) {
        const ctx = chartCanvas.value.getContext('2d');
        firstchartfirst = new Chart(ctx, {
          type: 'bar',
          data: {
            labels: [
              '情绪正面程度',
              '情绪负面程度',
              '今日情绪记录的可信度',
              '今日情绪稳定程度'
            ],
            datasets: [{
              label: '评估值',
              data: selfindicate,
              backgroundColor: ['#4CAF50', '#2196F3', '#FF9800', '#9C27B0'],
              borderRadius: 6,
              barThickness: 24
            }]
          },
          options: {
            responsive: true,
            indexAxis: 'y',
            plugins: {
              legend: { display: false },
              title: { display: true, text: '个人情绪指标图', font: { size: 18 } },
              tooltip: {
                callbacks: {
                  label: (context) => `${context.label}: ${context.raw} 分`
                }
              }
            },
            scales: {
              x: {
                min: 0, 
                title: { display: true, text: '得分分数' },
                ticks: { callback: (value) => value  }
              }
            }
          }
        });
      }


      // --- 第二个饼图 ---
      if (chartPie.value) {
        const ctx2 = chartPie.value.getContext('2d');
        const emotionDescriptions = {
          '喜悦': '那是时间留给生命的翩翩起舞。',
          '平静': '平常一般的平淡如水或许才是生活的真相。',
          '悲伤': '你哭了，我知道。',
          '愤怒': '很久很久以前，我也有如此的模样……',
          '复杂': '很少有纯粹的人，正如没有纯粹的情绪一样',
          '厌恶':'答应我，诀别的时候，不要这样……',
          '惊讶':'不期而遇的，是我们久别重逢的一见如故',
          '恐惧':'就像生如朝露，就像天暖人暮'
        };
        firstchartpie = new Chart(ctx2, {
          type: 'pie',
          data: {
            labels: ['喜悦', '悲伤', '愤怒', '惊讶', '厌恶','恐惧','复杂'],
            datasets: [{
              label: '本周情绪构成',
              data: selfmood,
              backgroundColor: ['#228B22', '#191970', '#DC143C', '#9932CC', '#A0522D','#DCDCDC','#B0C4DE'],
              borderWidth: 1
            }]
          },
          options: {
            responsive: true,
            plugins: {
              tooltip: {
                callbacks: {
                  label: (context) => {
                    const label = context.label;
                    const value = context.raw;
                    const description = emotionDescriptions[label] || '';
                    return `${label}: ${value}%\n${description}`;
                  }
                }
              },
              legend: { position: 'bottom' },
              title: { display: true, text: '今日情绪组成（百分比）', font: { size: 18 } }
            }
          }
        });
      }

    });

    return { chartCanvas, chartPie,animatefalse,selfhistory };
  }
};
</script>



<style scoped>
 
#protect{
  width: 100%;
  height: 100%;
  background-color: #73737356;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: 640;
  font-family: 'KaiTi';
  color:white;
  z-index:10;
  position: absolute;
  
}
#firstall{
  width: 100%;
  height: 100%;
  transform: translate(0,-10%);
  background-color: #ffffff61;
}
#firsttop{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;
}
#firstchartfirst{
  width: 60%;
  height: 100%;
}

ul{
  background-color: #ffffff77;
  width: 30%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;;
}
li{
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
li:hover{
  box-shadow: 0 0 3px 0 black;
  background-color: #51ffc5bd;
  animation:hoverup 0.2s ease
}
.firsttoppic{
  width: 18%;
  height: 80%;
}
p{
  font-size: 20px;
  font-weight: 550;
}
@keyframes hoverup {
  0%{
    transform: translate(0,0);
  }
  50%{
    transform: translate(0,-10%);
  }
  100%{
    transform: translate(0,0);
  }
}
#firstchartsecond{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;

}
#firstpiechart{
  width: 30%;
  height: 100%;
}
#firstchartfirst:hover,#firstpiechart:hover{
  background-color: #fff;
}
@keyframes setup {
  0%{
    transform: translate(0,20%);
    opacity: 0.2;
  }
  100%{
    transform: translate(0,0);
    opacity: 1;
  }
}
#firstchartfirst,#firstpiechart{
  animation:setup 0.8s ease;
}
#todolist{
  width: 60%;
  height: 100%;
}
</style>