<template>
  <transition>
    <div id="top"  class="animate__animated animate__flipInX animate__delay-0.5s">
      <img src="/src/62.png" id="toppic">
      <div id="topright">
        <div id="toprightmain">
          <div id="toprightinformation" @click="changeinformation">
            <img :src="information.firstsrc">
            <p>{{information.first}}</p>
          </div>
          <div id="toprightuser">
            <img src="/src/12.png" alt="">
            <a href="#">用户</a>
          </div>
          <div id="toprightsearch">
            <input type="text" class="search-txt" placeholder="搜索" >
            <button class="search-btn" :style="{'backgroundImage':'url(/src/3.png)'}"></button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import{reactive}from 'vue'
import 'animate.css'
import { eventBus } from '@/eventbus'
export default {
  setup(){
    let information=reactive({
      first:"消息",
      firstsrc:"/src/13.png",
      firstcount:0
    })
    function changeinformation(){
      if(information.firstcount%2==0){
        information.first="不通知"
        information.firstsrc="/src/15.png"
        eventBus.emit('setinformation',false)
      }
      else{
        information.first="消息"
        information.firstsrc="/src/13.png"
        eventBus.emit('setinformation',true)
        eventBus.emit('information','设置消息通知成功！')
      }
      information.firstcount+=1
    }
    return{
      information,
      changeinformation
    }

  }
}
</script>

<style scoped>
p,a{
  font-size: 20px;
  width: 45%;
  height: 50%;
  margin-top: 13%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#top{
  width: 100%;
  height: 12%;
  margin-bottom: 1%;
  border-radius: 1px;
  background-color: #ffffff2f;
  display: flex;
  justify-content: space-evenly;
}
#top:hover{
  background-color: #fff;
}
#toppic{
  width: 20%;
  height: 100%;
}
#topright{
  position:relative;
  width: 70%;
  height: 100%;
}
#toprightmain{
  width: 60%;
  height: 100%;
  position:absolute;
  right:0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
#toprightinformation,#toprightuser{
  width: 20%;
  height: 80%;
  display: flex;
  font-size: 20px;
  font-weight: 550;

  justify-content: center;
}
/* #toprightuserimg{
  height: 100%;
  aspect-ratio: 1;
} */
#toprightinformation:hover,#toprightuser:hover{
  background-color: #faff74;
  border: 1px solid rgba(0, 0, 0, 0.648);
 }
#toprightinformation:active{
  box-shadow: inset 1px 0 2px 0;
  transition:box-shadow 0s ease,background-color 0s ease;
  background-color: #b9fc35;
}
#toprightinformation:hover{
  cursor: pointer;
}

#toprightsearch{
  width: 50%;
  height: 60%;
  position: relative;
  background-color: #fff;
  box-shadow: 0 2px 25px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content:space-evenly;
  align-items: center;
  border-radius: 5px;
}
#toprightsearch:hover{
  border: 1px solid black;
}
.search-txt {
  height: 100%;
  width: 80%;
  padding: 10px;
  border: none;
  outline: none;
  font-size: 16px;
}
.search-txt:hover,.search-txt:focus{
  background-color: #c6c0c0;
}
.search-btn {
  width: 10%;
  height: 50%;
  /* background-image: url("/src/3.png"); */
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  border: none;
  cursor: pointer;
  border-radius: 50%;
}
.search-btn:hover{
  background-color: #a7a1a1;;
}
</style>