<template>
  <transition appear name="animate__animated animate__rubberBand"
     enter-active-class="animate__backInLeft"
     leave-active-class="animate__backOutLeft">
    <li @click="handlecheck(todo.id)" title="点击标记已完成" :class="{'done':todo.done}">
      <label>
        <input type="checkbox" :checked='todo.done' @change="handlecheck(todo.id)">
        &nbsp;&nbsp;
        <!--实际上v-model可以很快地完成上行代码的操作需求，但是不建议修改外部传参-->
        <span v-if="!todo.isedit">{{todo.title}}</span>
        <input ref="input" type="text" v-if="todo.isedit" @click.stop @keyup.enter="gogogo" :value= todo.title @blur="gogogo">
      </label>
      <button class="btn btn-danger" @click.stop="remove(todo.id)">删除</button>
      <button class="btn btn-edit" @click.stop="handleedit(todo)">编辑</button>
    </li>
  </transition>
</template>

<script setup>
import "animate.css";
import { ref, nextTick,provide } from "vue";
import { eventBus } from '@/eventbus';

// props 接收
const props = defineProps({
  todo: { type: Object, required: true },
  checktodo: { type: Function, required: true },
  removetodo: { type: Function, required: true }
});
const inputRef = ref(null);
const handlecheck = (id) => {
  props.checktodo(id);
};
const remove = (id) => {
  if (confirm("确认删除？")) {
    props.removetodo(id);
  }
};
const handleedit = () => {
  if (props.todo.isedit) {
    eventBus.emit('information','您已经在编辑状态了！')
    return;
  }
  props.todo.isedit = true;
  nextTick(() => {
    inputRef.value?.focus();
  });
};
const gogogo = (e) => {
  if (e.target.value === "") {
    props.todo.isedit = false;
    return;
  }
  var str=e.target.value
  eventBus.emit("edition", {id:props.todo.id, value:str});
  
  props.todo.isedit = false;
};
</script>

<style scoped>
  li{
    list-style:none;
    height: 48px;
    line-height: 48px;
    padding: 0 5px;
    font-size: 25px;
    border: 1px solid black;
    border-bottom: 0px solid black;
  }
  li label{
    float: left;
    cursor: pointer;
  }
  li label li input{
    vertical-align: middle;
    margin-right: 6px;
    position:relative;
    top:-1px
  }
  li button{
    float:right;
    display: none;
    margin-top: 3px;
  }
  li:before{
    content: initial;
  }
  li:last-child{
    border-bottom: none;
  }
  li:hover{
    background-color: #6596ff;
  }
  li:hover button{
    display: block;
  }
  .done{
    background-color: #61d6fd;
    font-weight: 20px;
    font-size: 660;
  }
  .btn{
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px rgba(255,255,255,0.2),0 1px 2px rgba(0,0,0,0.5);
  border-radius: 4px;
}
.btn-danger{
  color:#fff;
  background-color: #da4f49;
  border: 1px solid red;
}
.btn-danger:hover{
  color:#fff;
  background-color: #bd362f;
}
.btn:focus{
  outline:none;
}
.btn-edit{
  color:#fff;
  background-color: #49a5da;
  border: 1px solid rgb(0, 76, 255);
  margin-right: 5px;
}
.btn-edit:hover{
  color:#fff;
  background-color: #198f58;
}

</style>