<template>
  <nav :style="navstyle" class="animated-bg">
    <div id="information" v-if="informationin" class="information" :class="{'animate__fadeInDown animate__animated':informationin,'animate__fadeOutDown animate__animated':informationout}">
      {{ new_information }}
    </div>
    <maintop/>
    <mainmiddle :pagenum="pagenum" :alldata="alldata" :actionhistory="actionhistory">
    </mainmiddle>
    <mainmenu v-if="isChange" @close="closepage" @turntopage="goforpage"></mainmenu>


    
    
  <div id="bottom" :class="{ 'bottom-active': isActive,'bottom-hover':isHover
   }"   @click="changebottomstyle" @mouseenter="isHover=true" @mouseleave="isHover=false">
      点击切换功能页
  </div>
  </nav>
  
</template>

<script>
import{h,reactive,ref,onMounted,onUnmounted,nextTick, watch} from 'vue'
import maintop from './main/maintop.vue'
import mainmiddle from './main/mainmiddle.vue'
import mainmenu from './main/menu.vue'
import { eventBus } from '@/eventbus'
export default {
  components:{
    maintop,
    mainmiddle,
    mainmenu
  },
  emit:['changepage'],
  setup(props,{emit}) {
    var actionhistory=reactive([])
    var actiontime=ref(1)
    var new_information=ref("你好世界")
    var informationin=ref(false)
    var informationout=ref(false)
    var setinformation=ref(true)
    const nowaday=new Date()
    if(localStorage.getItem("alldata")){
      const storedData = JSON.parse(localStorage.getItem('alldata'));
      var alldata = reactive(storedData);
    }else{
      var alldata = reactive({
        [nowaday.getFullYear()]: {
          [nowaday.getMonth()]: {
            [nowaday.getDate()]: {
              mood: [0,0,0,0,0,0,0],
              /*'喜悦', '悲伤', 
              '愤怒', '惊喜', 
              '厌恶',,'恐惧','复杂'
              */
              indicator: [0,0,0,0],
              history: [0, 0, 0, 0],
              moodhistory:[0,0,0,0,0,0,0],
              music:0,
              diary:'',
              img:'',
              sound:''
            }
          }
        }
      })

       localStorage.setItem("alldata", JSON.stringify(alldata));
    }
    const year = nowaday.getFullYear();
    const month = nowaday.getMonth(); // 月份从 0 开始
    const date = nowaday.getDate();
    function updatealldata(year,month,date){
      
      if (!alldata[year]) {
        alldata[year] = {};
      }

      if (!alldata[year][month]) {
        alldata[year][month] = {};
      }

      if (!alldata[year][month][date]) {
        alldata[year][month][date] = {
          mood: [0,0,0,0,0,0,0],
              /*'喜悦', '悲伤', 
              '愤怒', '惊喜', 
              '厌恶',,'恐惧','复杂' */
            indicator: [0,0,0,0],
            history: [0, 0, 0, 0],
            moodhistory:[0,0,0,0,0,0,0],
            music:0,
            diary:'',
            img:'',
            sound:''
        };
      }
      localStorage.setItem("alldata", JSON.stringify(alldata));
    }
    updatealldata(year,month,date)

    function changehistory(man)
    {
      var year=man.year
      var month=man.month
      var day=man.day
      var where=man.where
      var time=man.time
      actionhistory.push(`${actiontime.value}.${man.action}`)
      actiontime.value++
      updatealldata(year,month,day)
      alldata[year][month][day].history[where]+=time
      localStorage.setItem("alldata", JSON.stringify(alldata));
    }
    function changemoodhistory(man)
    {
      console.log(man)
      var year=man.year
      var month=man.month
      var day=man.day
      var where=man.where
      var time=man.time
      console.log("具体位置",where)
      updatealldata(year,month,day)
      console.log("过往历史",alldata[year][month][day].moodhistory)
      alldata[year][month][day].moodhistory[where]+=time
      console.log("具体情况",alldata[year][month][day].moodhistory[where])
      localStorage.setItem("alldata", JSON.stringify(alldata));
    }
    function changediary(man)
    {
      var year=man.year
      var month=man.month
      var day=man.day
      var diaryvalue=man.diary
      updatealldata(year,month,day)
      alldata[year][month][day].diary=diaryvalue
      localStorage.setItem("alldata", JSON.stringify(alldata));

    }
    function changeindicator(man)
    {
      var year=man.year
      var month=man.month
      var day=man.day
      updatealldata(year,month,day)
      alldata[year][month][day].indicator[0]=man.firstvalue
      alldata[year][month][day].indicator[1]=man.secondvalue
      alldata[year][month][day].indicator[2]=man.thirdvalue
      alldata[year][month][day].indicator[3]=man.forthvalue

      console.log("man",man)
      console.log(alldata[year][month][day])
      localStorage.setItem("alldata", JSON.stringify(alldata));
    }
    watch(alldata,(newv,oldev)=>{
      localStorage.setItem('alldata',JSON.stringify(newv))
    })
    function changeaction(man)
    {
      actionhistory.push(`${actiontime.value}.${man.action}`)
      actiontime.value++
    }












    var navstyle=reactive({
      width:window.innerWidth+'px',
      height:window.innerHeight+'px'
    })
    
    var isActive=ref(false)
    var isHover=ref(false)
    var isChange=ref(false)

    function changebottomstyle(){
      isActive.value =true;   
      console.log(isActive.value)
      setTimeout(() => {
        isActive.value=false
        changepage()
      }, 500);
      
    }
    function changepage()
    {
      isChange.value=true
      console.log(isChange.value)
    }
    function closepage(data){
      isChange.value=data
    }
    var pagenum=ref(0)
    function goforpage(n){
      closepage(false)
      pagenum.value=n
    }
    
    function fifthupdatealldata(man)
    {
      console.log("主程序增加了这个日期")
      var ayear=man.year
      var amonth=man.month
      var aday=man.day
      updatealldata(ayear,amonth,aday)
      eventBus.emit('datechange1',{year:ayear,month:amonth,day:aday})
    }
    function changeinformation(inner)
    {
      if(!setinformation.value)return
      informationout.value=false
      new_information.value=inner
      informationin.value=true
      setTimeout(() => {
        informationout.value=true
        setTimeout(() => {
          informationin.value=false
          new_information.value=''
        }, 1151);
      }, 1151);
    }




    const resize=()=>{
      navstyle.width=`${window.innerWidth}px`
      navstyle.height=`${window.innerHeight}px`
    }
    onMounted(()=>{
      window.addEventListener('resize',resize)
      eventBus.on('changehistory',changehistory)
      eventBus.on('changemoodhistory',changemoodhistory)
      eventBus.on('diarychange',changediary)
      eventBus.on('datechange',fifthupdatealldata)
      eventBus.on('changeindicator',changeindicator)
      eventBus.on('action',changeaction)
      eventBus.on('information',changeinformation)
      eventBus.on('setinformation',(man)=>{
        setinformation.value=man
      })
    })
    onUnmounted(()=>{
      window.removeEventListener('resize',resize)
      eventBus.off('changehistory',changehistory)
      eventBus.off('changemoodhistory',changemoodhistory)
      eventBus.off('diarychange',changediary)
      eventBus.off('datechange',fifthupdatealldata)
      eventBus.off('changeindicator',changeindicator)
      eventBus.off('action',changeaction)
      eventBus.off('information',changeinformation)
      eventBus.off('setinformation',(man)=>{
        setinformation.value=man
      })
    })
    return {
      navstyle,
      changebottomstyle,
      isActive,
      isHover,
      isChange,
      closepage,
      goforpage,
      pagenum,
      alldata,
      actionhistory,
      new_information,
      informationin,
      informationout


    }
  },
}
</script>

<style scoped>

nav{
  width: 100%;
  height: 100%;
}


  

#bottom{
  width: 100%;
  height: 7%;
  background: linear-gradient(135deg, #53535379, #f1ff5a76, #4c4c4c69); 
  border: 2px solid rgb(0, 0, 0);
  font-family: 'KaiTi';
  font-size: 35px;
  font-weight: 560;
  display: flex;
  justify-content: center;
  align-items: center;
}
#bottom:hover{
  background-color: #59ffff92;
  cursor: pointer;
  font-weight: 620;

}
.bottom-hover{
  animation: hover ease 8s;
}
.bottom-active{
  animation: click 0.5s ease-in;
}
.animated-bg {
  position: relative;
  background: linear-gradient(45deg, 
      #ff868645, #ffb47774, #fffd9066, #96ff7073, 
      #6fffe782, #6b84ff71, #bf85fd98, #f986e088, #ff000045);
  background-size: 1000% 1000%;
  animation: gradientFlow 15s ease infinite;
}

/* 定义动画关键帧 */
@keyframes gradientFlow {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
/*—————————————————————————————————————————————————————————— */
.information{
  width: 20%;
  height: 12%;
  position: absolute;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-weight: 620;
  color:#ffffff;
  background-color: #69f794;
  font-family: 'KaiTi';
  border-radius: 12px;
  z-index: 201;
  border-left: 25px solid rgb(0, 255, 225);
  box-shadow: 10px 0 10px 0 rgba(7, 7, 7, 0.792);
  left:40%;
  top:5%
}


</style>