<template>
 <div id="thirdtimeassistant" class="thirdcontainer">
    <!-- 上半：四个音频选项 -->
    <div id="thirdtoprow" class="thirdtoprow">
      <button class="thirtopaudio" @click="thirdPlayAudio('wind')">风</button>
      <button class="thirtopaudio" @click="thirdPlayAudio('rain')">雨</button>
      <button class="thirtopaudio" @click="thirdPlayAudio('bird')">鸟</button>
      <button class="thirtopaudio" @click="thirdPlayAudio('stream')">溪</button>
    </div>

    <!-- 下半：功能主体 -->
    <div id="thirdmain" class="thirdmain">
      <!-- 左侧功能栏 -->
      <div id="thirdleftbar" class="thirdleftbar">
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(1)">增加1分钟</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(5)">增加5分钟</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(30)">增加30分钟</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-1)">减少1分钟</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-5)">减少5分钟</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-30)">减少30分钟</button>
        <button class="thirdctrlbtn" @click="thirdAddWaterReminder">增加喝水提醒</button>
        <button class="thirdctrlbtn" @click="thirdSetRestPoint">设置休息点</button>
        <button class="thirdctrlbtn" @click="thirdSwitchToRelief">缓解情绪</button>
      </div>

      <!-- 右侧主体：通过 v-if 切换两个视图 -->
      <div id="thirdrightarea" class="thirdrightarea">
        <!-- 计时助手视图 -->
        <div v-if="thirdActiveView === 'timer'" id="thirdtimerview" class="thirdtimerview">
          <!-- 第三级上部：大时间显示（占宽至少85%、高至少50%） -->
          <div id="thirdtimedisplaywrap" class="thirdtimedisplaywrap">
            <div id="thirdtimedisplay" class="thirdtimedisplay">
              {{ thirdDisplayTime }}
            </div>
          </div>

          <!-- 第三级中部：进度条（占宽至少90%） -->
          <div id="thirdprogresswrap" class="thirdprogresswrap">
            <div class="thirdprogressbar">
              <div
                class="thirdprogressinner"
                :style="{
                  width: thirdProgressPercent + '%',
                  backgroundColor: thirdProgressColor
                }"
              ></div>
            </div>

            <!-- 显示现有的提醒点（小色块与提示） -->
            <div class="thirdmarkerswrap">
              <div
                v-for="(m, idx) in thirdMarkers"
                :key="idx"
                class="thirdmarker"
                :style="{ backgroundColor: m.color, left: (m.minute / 120 * 100) + '%' }"
                :title="m.type === 'water' ? '喝水提醒: ' + m.minute + ' 分钟' : '休息点: ' + m.minute + ' 分钟'"
              ></div>
            </div>
          </div>

          <!-- 第三级下部：控制按钮 -->
          <div id="thirdcontrols" class="thirdcontrols">
            <button class="thirdactionbtn" @click="thirdStart">开始</button>
            <button class="thirdactionbtn" @click="thirdPause">暂停</button>
            <button class="thirdactionbtn" @click="thirdImmediateEnd">立即结束</button>
          </div>
        </div>

        <!-- 缓解情绪资料视图 -->
        <div v-if="thirdActiveView === 'relief'" id="thirdreliefview" class="thirdreliefview">
          <div class="thirdreliefgrid">
            <div class="thirdreliefcard" v-for="(item, idx) in thirdReliefItems" :key="idx">
              <img :src="item.img" alt="" class="thirdreliefimg" />
              <div class="thirdreliefbody">
                <h3 class="thirdrelieftitle">{{ item.title }}</h3>
                <p class="thirdreliefdesc">{{ item.desc }}</p>
                <a class="thirdrelieflink" :href="item.link" target="_blank">了解更多</a>
              </div>
            </div>
          </div>

          <div class="thirdbackbtnwrap">
            <button class="thirdactionbtn" @click="thirdSwitchToTimer">返回计时</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from "@/eventbus";
import { ref, reactive, computed, onUnmounted } from "vue";

export default {
  name: "ThirdTimeAssistant",
   setup() {
    // ---------- 音频路径占位（你后续补充真实路径即可） ----------
    const thirdAudioPaths = reactive({
      wind: "/src/68.m4a", 
      rain: "/src/69.m4a",
      bird: "/src/70.m4a",
      stream: "/src/71.m4a"
    });

    let thirdAudioObject = null;
    let audionum=0
    const thirdPlayAudio = (which) => {
      const whichname={
        'wind':'风',
        'rain':'雨',
        'bird':'鸟',
        'stream':'溪'
      }
      // 停掉旧音频
      console.log("音频现在的数字",audionum)
      try {
        if (thirdAudioObject&&audionum%2!==0) {
          thirdAudioObject.pause();
          thirdAudioObject.currentTime = 0;
          eventBus.emit('information','已暂停！')
          audionum++
          return
        }
      } catch (e) { /* ignore */ }

      // 创建新音频（路径占位，后续替换）
      if (thirdAudioPaths[which]&&audionum%2===0) {
        thirdAudioObject = new Audio(thirdAudioPaths[which]);
        thirdAudioObject.loop = true;
        audionum++
        eventBus.emit('information',`正在播放·${whichname[which]}，再次点击可暂停`)
        eventBus.emit('changeaction',{action:`<p id=actionmain>您使用了<strong>情绪调频</strong>并播放了白噪音<strong>《${whichname[which]}》</strong></p>`})
        thirdAudioObject.play().catch((e) => {
          console.warn("音频播放失败，可能浏览器限制自动播放：", e);
        });
      } else {
        // 占位提示（不阻塞逻辑）
        console.log("播放音频占位：", which);
      }
    };

    // ---------- 计时相关 state ----------
    const thirdConfiguredMinutes = ref(0); // 用户配置的总分钟数（0~120）
    const thirdRemainingSeconds = ref(0); // 当前剩余秒数
    const thirdTimerInterval = ref(null);
    const thirdIsRunning = ref(false);

    // markers: { type: 'water'|'rest', minute: Number, color: String, triggered: Boolean }
    const thirdMarkers = ref([]);

    // 进度条颜色管理
    const presetColors = [
      "#ff7f7f", "#7fbfff", "#7fffbf", "#ffd27f", "#c57fff", "#7fffff", "#ffb3ff"
    ];
    const thirdUsedColors = ref([]);
    const thirdProgressColor = ref(presetColors[0]);

    // 视图切换
    const thirdActiveView = ref("timer"); // 'timer' 或 'relief'

    // 情绪缓解内容示例
    const thirdReliefItems = ref([
      { title: "情绪指南针", desc: "导向您的学习，明白如何处理您的情绪", img: "/src/65.png", link: "https://emotioncompass.org/zh/" },
      { title: "心理服务", desc: "中国国民心理健康服务，爱让生命更美好", img: "/src/66.png", link: "http://psy.china.com.cn/2023-06/20/content_42329815.htm" },
      { title: "FeelGoodPal", desc: "营养、健康和幸福的建议", img: "/src/67.png", link: "https://feelgoodpal.com/zh/blog/ways-to-relieve-stress-and-anxiety/" }
    ]);

    // ---------- 进度条宽度（百分比） ----------
    const thirdProgressPercent = computed(() => {
      return (thirdConfiguredMinutes.value / 120) * 100;
    });

    // ---------- 显示倒计时 mm:ss ----------
    const formatTime = (sec) => {
      const s = Math.max(0, Math.floor(sec));
      const mm = Math.floor(s / 60)
        .toString()
        .padStart(2, "0");
      const ss = (s % 60).toString().padStart(2, "0");
      return `${mm}:${ss}`;
    };

    const thirdDisplayTime = computed(() => {
      // 若未设置时间，显示 00:00
      if (!thirdRemainingSeconds.value && !thirdConfiguredMinutes.value) {
        return "00:00";
      }
      // 若倒计时未启动，但配置了分钟，显示配置的分钟为倒计时（分钟:00）
      if (!thirdIsRunning.value && thirdConfiguredMinutes.value) {
        return `${String(thirdConfiguredMinutes.value).padStart(2, "0")}:00`;
      }
      return formatTime(thirdRemainingSeconds.value);
    });

    // ---------- 增减分钟（限制 0~120） ----------
    const thirdAdjustMinutes = (delta) => {
      const prior = thirdConfiguredMinutes.value;
      let newVal = prior + delta;
      if (newVal > 120) newVal = 120;
      if (newVal < 0) newVal = 0;

      // 计算真正的变化量
      const diff = newVal - prior;
      thirdConfiguredMinutes.value = newVal;

      // 若计时正在运行，也需要调整剩余秒数（把改变量应用到 remaining）
      if (thirdIsRunning.value && diff !== 0) {
        thirdRemainingSeconds.value = Math.max(0, thirdRemainingSeconds.value + diff * 60);
      } else if (!thirdIsRunning.value) {
        // 若未运行，保持 remaining 为 0（显示使用 configured）或更新为配置分钟
        thirdRemainingSeconds.value = newVal > 0 ? newVal * 60 : 0;
      }
    };

    // ---------- 为提醒选择新颜色（不与已有颜色相同） ----------
    const thirdPickNewColor = () => {
      for (const c of presetColors) {
        if (!thirdUsedColors.value.includes(c)) {
          thirdUsedColors.value.push(c);
          return c;
        }
      }
      // 如果用尽预设颜色，生成随机颜色并确保不与已有重复
      let candidate;
      do {
        candidate = "#" + Math.floor(Math.random() * 16777215).toString(16).padStart(6, "0");
      } while (thirdUsedColors.value.includes(candidate));
      thirdUsedColors.value.push(candidate);
      return candidate;
    };

    // ---------- 添加喝水提醒 ----------
    const thirdAddWaterReminder = () => {
      // 将 marker 的 minute 设为当前配置分钟（整数）
      const minute = Math.max(0, Math.min(120, Math.ceil(thirdConfiguredMinutes.value)));
      const color = thirdPickNewColor();
      thirdMarkers.value.push({
        type: "water",
        minute,
        color,
        triggered: false
      });
      // 切换进度条颜色为该提醒颜色
      thirdProgressColor.value = color;
      console.log("已添加喝水提醒，minute=", minute);
    };

    // ---------- 设置休息点 ----------
    const thirdSetRestPoint = () => {
      const minute = Math.max(0, Math.min(120, Math.ceil(thirdConfiguredMinutes.value)));
      const color = thirdPickNewColor();
      thirdMarkers.value.push({
        type: "rest",
        minute,
        color,
        triggered: false
      });
      thirdProgressColor.value = color;
      console.log("已设置休息点，minute=", minute);
    };

    // ---------- 触发提醒处理函数（只 console.log） ----------
    const thirdHandleMarkerTriggered = (marker) => {
      if (marker.type === "water") {
        console.log("喝水");
      } else if (marker.type === "rest") {
        console.log("休息");
      }
      marker.triggered = true;
    };

    // ---------- 启动倒计时 ----------
    const thirdStart = () => {
      // 如果没有配置分钟，则不启动
      if (thirdConfiguredMinutes.value <= 0 && thirdRemainingSeconds.value <= 0) return;

      // 如果还没有 remaining（即未设置或已结束），用 configured 来初始化
      if (!thirdRemainingSeconds.value) {
        thirdRemainingSeconds.value = thirdConfiguredMinutes.value * 60;
      }

      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      eventBus.emit("action",{action:"<p id=actionmain>您使用了情绪调频中的<strong>休憩助手</strong></p>"})
      thirdIsRunning.value = true;
      thirdTimerInterval.value = setInterval(() => {
        if (thirdRemainingSeconds.value > 0) {
          thirdRemainingSeconds.value -= 1;

          // 每秒检查是否到达某个提醒的整分钟（以分钟为单位比较）
          const remainingMinutesCeil = Math.ceil(thirdRemainingSeconds.value / 60);
          thirdMarkers.value.forEach((m) => {
            if (!m.triggered && remainingMinutesCeil === m.minute) {
              thirdHandleMarkerTriggered(m);
            }
          });
        } else {
          // 倒计时结束
          clearInterval(thirdTimerInterval.value);
          thirdTimerInterval.value = null;
          thirdIsRunning.value = false;
          // 到时可以额外处理
          console.log("计时结束");
        }
      }, 1000);
    };

    // ---------- 暂停 ----------
    const thirdPause = () => {
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      thirdIsRunning.value = false;
    };

    // ---------- 立即结束 ----------
    const thirdImmediateEnd = () => {
      if (thirdRemainingSeconds.value > 0) {
        const ok = confirm("倒计时尚未结束，是否确定立即结束？");
        if (!ok) return;
      }
      // 清空并回到初始状态
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      thirdIsRunning.value = false;
      thirdRemainingSeconds.value = 0;
      thirdConfiguredMinutes.value = 0;
      // 重置 markers & colors（可选）
      thirdMarkers.value = [];
      thirdUsedColors.value = [];
      thirdProgressColor.value = presetColors[0];
    };

    // ---------- 视图切换函数 ----------
    const thirdSwitchToRelief = () => {
      thirdActiveView.value = "relief";
    };
    const thirdSwitchToTimer = () => {
      thirdActiveView.value = "timer";
    };

    // ---------- 清理 interval ----------
    onUnmounted(() => {
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
      }
      if (thirdAudioObject) {
        try {
          thirdAudioObject.pause();
          thirdAudioObject = null;
        } catch (e) {}
      }
    });

    // ---------- 暴露到模板 ----------
    return {
      // audio
      thirdPlayAudio,
      // states
      thirdConfiguredMinutes,
      thirdRemainingSeconds,
      thirdMarkers,
      thirdProgressPercent,
      thirdProgressColor,
      thirdDisplayTime,
      thirdIsRunning,
      thirdActiveView,
      thirdReliefItems,
      // actions
      thirdAdjustMinutes,
      thirdAddWaterReminder,
      thirdSetRestPoint,
      thirdStart,
      thirdPause,
      thirdImmediateEnd,
      thirdSwitchToRelief,
      thirdSwitchToTimer
    };
  }
};
</script>

<style scoped>
 /* 主容器 - 玻璃态效果 */

#thirdtimeassistant.thirdcontainer {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  border-radius: 20px; 
  padding: 20px;
  box-shadow: 0 8px 32px rgba(31, 38, 135, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

/* 动态流动背景 */
.thirdcontainer::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg, 
    rgba(255, 36, 36, 0.329) 0%, 
    rgba(156, 250, 33, 0.436) 25%, 
    rgba(35, 255, 185, 0.47) 50%, 
    rgba(92, 61, 244, 0.381) 75%, 
    rgba(255, 8, 111, 0.406) 100%
  );
  z-index: -1;
  transform: rotate(0deg);
  animation: flowBackground 15s linear infinite;
}

@keyframes flowBackground {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 顶部音频按钮 */
#thirdtoprow {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 15px;
}

.thirtopaudio {
  width: 22%;
  height: 70%;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.thirtopaudio:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background-color: #5d5d5dbf;
}

.thirtopaudio:active {
  transform: translateY(1px);
}

/* 主内容区域 */
#thirdmain {
  display: flex;
  width: 100%;
  height: 80%;
  box-sizing: border-box;
  gap: 15px;
}

/* 左侧功能栏 */
#thirdleftbar {
  width: 22%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 15px;
  box-sizing: border-box;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  overflow-y: auto;
}

.thirdctrlbtn {
  width: 100%;
  padding: 12px 5px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.thirdctrlbtn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateX(5px);
  background-color: #38383898;
}

/* 右侧区域 */
#thirdrightarea {
  width: 78%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
}

/* 时间显示区域 - 3D立体效果 */
#thirdtimedisplaywrap {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  box-sizing: border-box;
}

#thirdtimedisplay {
  width: 95%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 120px;
  border-radius: 15px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.2);
  color: white;
  text-shadow: 3px 3px 0 #0c0c0c, 
              6px 6px 0 rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.3),
              0 10px 20px rgba(0, 0, 0, 0.2);
  font-weight: 660;
  letter-spacing: 2px;
}

/* 进度条区域 */
#thirdprogresswrap {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 15px;
  position: relative;
}

.thirdprogressbar {
  width: 90%;
  height: 30%;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.3);
}

.thirdprogressinner {
  height: 100%;
  width: 0%;
  transition: width 0.3s linear, background-color 0.3s linear;
  border-radius: 10px;
}

/* 提醒点标记 */
.thirdmarkerswrap {
  width: 90%;
  height: 10%;
  position: relative;
  margin-top: 10px;
}

.thirdmarker {
  position: absolute;
  top: 0%;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

/* 控制按钮 */
#thirdcontrols {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center;
  gap: 20px;
  align-items: center;
}

.thirdactionbtn {
  width: 25%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.thirdactionbtn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-5px);
  background-color: #59595993;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
}

/* 缓解情绪视图 */
#thirdreliefview {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 10px;
}

.thirdreliefgrid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin-bottom: 15px;
}

.thirdreliefcard {
  box-sizing: border-box;
  padding: 15px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.thirdreliefcard:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.thirdreliefimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 10px;
  border-radius: 10px;
}

.thirdreliefbody {
  width: 100%;
  text-align: center;
}

.thirdrelieftitle {
  color: white;
  margin-bottom: 10px;
  font-size: 1.2rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.thirdreliefdesc {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 10px;
  font-size: 0.9rem;
}

.thirdrelieflink {
  color: #a3d9ff;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.thirdrelieflink:hover {
  color: #7bcbff;
  text-decoration: underline;
}

/* 返回按钮 */
.thirdbackbtnwrap {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: auto;
}

/* 响应式设计 */
@media (max-width: 900px) {
  #thirdmain {
    flex-direction: column;
  }
  
  #thirdleftbar, #thirdrightarea {
    width: 100%;
  }
  
  #thirdleftbar {
    height: auto;
    max-height: 200px;
    flex-direction: row;
    flex-wrap: wrap;
    overflow-x: auto;
  }
  
