<template>
  <div id="secondleft">
    <ul>
      <li @click="secondpage(0)" title="点击进入文本识别" class="animate-item" :class="{'bechosen':choice[0]}">文本识别</li>
      <li @click="secondpage(1)" title="点击进入文本识别" class="animate-item" :class="{'bechosen':choice[1]}">图片识别</li>
      <li @click="secondpage(2)" title="点击进入文本识别" class="animate-item" :class="{'bechosen':choice[2]}">音频识别</li>
      <li @click="secondpage(3)" title="点击进入文本识别" class="animate-item" :class="{'bechosen':choice[3]}">脑电识别</li>
    </ul>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus';
import {reactive,ref,onMounted,onUnmounted} from'vue'
export default {
  setup(){
    var now=ref(null)
    var choice=reactive([false,false,false,false])
    if(now.value)choice[now.value]=true
    function secondpage(n){
      const chosename=['文本识别','图片识别','音频识别','脑电识别']
      eventBus.emit('information',`选择：${chosename[n]}`)
      eventBus.emit('startsecond',n)
      now.value=n
      choice.forEach((el,index)=>{
        choice[index]=false
      })
      choice[n]=true
      eventBus.emit('closere',false)
    }
    function getout(man)
    {
      choice.forEach((el,index)=>{
        choice[index]=man
      })
    }
    onMounted(()=>{
      eventBus.on('closesecondmain',getout)
    })

    return{
      secondpage,
      choice
    }
  }
}
</script>

<style scoped>
 
#secondleft{
  width: 23%;
  height: 85%;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0) rotateX(1deg) rotateY(1deg);
  }

  50% {
    transform: translateY(-3%) rotateX(-1deg) rotateY(-1deg);
  }
}
ul{
  width: 100%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  animation: float 1s ease infinite;
}
ul:hover{
  background-color: #ffd9d9d2;
  animation: float 5s ease infinite;
}

li{
  width: 80%;
  height: 20%;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight:620;
  font-size: 40px;
  font-family: 'KaiTi';
  background-color: #e8d0ffd8;
  transition: transform 0.2s ease;
  background-repeat: no-repeat;
  background-size: 28% 50%;
  background-position: right;
}
li:hover{
  transform: translate(0,-8%);
  background-color: #a2fffc;
  cursor: pointer;
  border: 1px solid black;
  background-image: url("../../assets/53.png");
}
.animate-item {
    transform: translateY(100%);
    opacity: 0;
    transition: transform 0.5s ease, opacity 0.5s ease;
}

.animate-item:nth-child(1) {
    animation: fadeInUp 0.5s forwards;
    animation-delay: 0s;
}

.animate-item:nth-child(2) {
    animation: fadeInUp 0.5s forwards;
    animation-delay: 0.2s;
}

.animate-item:nth-child(3) {
    animation: fadeInUp 0.5s forwards;
    animation-delay: 0.4s;
}

.animate-item:nth-child(4) {
    animation: fadeInUp 0.5s forwards;
    animation-delay: 0.6s;
}

@keyframes fadeInUp {
  0% {
      transform: translateY(100%);
      opacity: 0;
  }
  100% {
      transform: translateY(0);
      opacity: 1;
  }
}
.bechosen{
  background-color: #1787ff93;
  background-image: url("../../assets/53.png");
  border: 1px solid black;
}
</style>