import { createRouter, createWebHashHistory } from 'vue-router'
import firstall from '@/components/first/firstall.vue'
import home from '@/components/home.vue';
import secondmain from '@/components/second/secondmain.vue';
import thirdmain from '@/components/third/thirdmain.vue';
import forthmain from '@/components/forth/forthmain.vue';
import fifthmain from '@/components/fifth/fifthmain.vue';
import firstrightfirst from '@/components/first/firstright/firstrightfirst.vue';
import firstrightsecond from '@/components/first/firstright/firstrightsecond.vue';
import firstrightthird from '@/components/first/firstright/firstrightthird.vue';
const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/first',
    name: 'first',
    component: firstall
  },
  {
    name: 'first_first',
    path: '/first_firstc',
    component: firstrightfirst
  },
  {
    name: 'first_second',
    path: '/first_secondc',
    component: firstrightsecond
  },
  {
    name: 'first_third',
    path: '/first_thirdc',
    component: firstrightthird
  },
  {
    path: '/second',
    name: 'second',
    component: secondmain
  },
  {
    path: '/third',
    name: 'third',
    component: thirdmain
  },
  {
    path: '/forth',
    name: 'forth',
    component: forthmain
  },
  {
    path: '/fifth',
    name: 'fifth',
    component: fifthmain
  }


]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
