<template>
  <div id="soundre">
    <br><hr>
    <h1>识别的结果为：</h1>
    <br><hr>
    <p>{{ picre[props.result.data.trim()] }}</p>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus'
export default {
  props:['result'],
  setup(props){
    const nowaday=new Date()
    const picdatachange={
      '高兴':0,
      '悲伤':1,
      '愤怒':2,
      '惊讶':3,
      '厌恶':4,
      '恐惧':5,
      '平静':6
    }
    const picre={
      '高兴':'高兴😄',
      '悲伤':'悲伤😢',
      '愤怒':'愤怒😠',
      '惊讶':'惊讶😮',
      '厌恶':'厌恶🤢',
      '恐惧':'恐惧😨',
      '平静':'平静😐'
    }
    console.log("data",props.result.data)
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:'<p class=actionmain>您使用了音频识别，识别结果是<strong>'+props.result.data+'</strong></p>'})
    eventBus.emit('changemoodhistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:picdatachange[props.result.data.trim()],time:1,})
    // eventBus.emit('actionhistory',{action:`<p class=actionmain>您刚刚使用了音频识别，识别结果是<strong>${picre[props.result.data.trim()]}</strong></p>`})
    return {
      props,picre
    }
  }
}
</script>

<style scoped>

#soundre{
  width: 100%;
  height: 100%;
}
#soundre:hover{
  background-color: #ffffff9d;
}
p{
  font-size: 50px;
  font-weight: 650;
  width: 70%;
  height: 60%;
  font-family: 'KaiTi';
  margin:5% auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>