<template>
  <div class="date-container" :class="{'date-animation': allmove}" @mouseenter="endmove" @mouseleave="startmove">
    <div class="date-year">{{ currentDate.year }}</div>
    <div class="date-month-day">{{ currentDate.month }}{{ currentDate.day }}</div>
    <div class="date-time">{{ currentDate.hours }}:{{ currentDate.minutes }}:{{ currentDate.seconds }}</div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';

export default {
  setup() {
    const allmove = ref(true); 
    const currentDate = ref({
      year: '',
      month: '',
      day: '',
      hours: '',
      minutes: '',
      seconds: ''
    });

    const updateDate = () => {
      const now = new Date();
      currentDate.value = {
        year: now.getFullYear(),
        month: now.toLocaleString('default', { month: 'long' }),
        day: now.toLocaleString('zh-CN', { day: 'numeric' }),
        hours: String(now.getHours()).padStart(2, '0'),
        minutes: String(now.getMinutes()).padStart(2, '0'),
        seconds: String(now.getSeconds()).padStart(2, '0')
      };
    };

    onMounted(() => {
      updateDate(); // 初始化日期
      const intervalId = setInterval(updateDate, 1000); 
      onUnmounted(() => {
        clearInterval(intervalId);
      });

      startmove();
    });

    const startmove = () => {
      allmove.value = true; 
      clearInterval(movetime.value); 
      movetime.value = setInterval(() => {
        allmove.value = false; 
        setTimeout(() => {
          allmove.value = true; 
        }, 10000);
      }, 10000);
    };

    const endmove = () => {
      allmove.value = false; 
      clearInterval(movetime.value); 
    };

    const movetime = ref(null); 

    return {
      currentDate,
      allmove,
      startmove,
      endmove
    };
  }
};
</script>

<style scoped>
@keyframes hoverup {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, -10%);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes fudong {
  0% {
    transform: translate(0, 0);
  }
  6.25% {
    transform: translate(0, -2%);
  }
  12.5% {
    transform: translate(0, -4%);
  }
  18.75% {
    transform: translate(0, -6%);
  }
  25% {
    transform: translate(0, -8%);
  }
  31.25% {
    transform: translate(0, -6%);
  }
  37.5% {
    transform: translate(0, -4%);
  }
  43.75% {
    transform: translate(0, -2%);
  }
  50% {
    transform: translate(0, 0);
  }
  56.25% {
    transform: translate(0, 2%);
  }
  62.5% {
    transform: translate(0, 4%);
  }
  68.75% {
    transform: translate(0, 6%);
  }
  75% {
    transform: translate(0, 8%);
  }
  81.25% {
    transform: translate(0, 6%);
  }
  87.5% {
    transform: translate(0, 4%);
  }
  93.75% {
    transform: translate(0, 2%);
  }
  100% {
    transform: translate(0, 0);
  }
}

.date-container {
  text-align: center;
  background-color: #cfffaf;
  width: 85%;
  height: 20%;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.1s ease;
}
.date-container:hover{
  box-shadow: 0 0 10px #ccc;
  animation: hoverup 1s ease;
}
.date-year {
  font-weight: 680;
  font-size: 20px;
  color: #02b43a;
  margin-top: 1%;
}

.date-month-day {
  font-size: 48px;
  color: #097e05;
  font-family: 'KaiTi';
  margin-bottom: 1%;
  font-weight: 700;
}

.date-time {
  font-weight: 680;
  font-size: 18px;
  color: #22d847;
}
.date-animation {
  animation: fudong 2.5s ease infinite; 
}
</style>