<template>
  <div id="first">
    <div id="mainleft">
      <firstlefttop/>
      <firstleftmain :actionhistory="props.actionhistory"/>
      
    </div>
    
    <div id="mainright">
      <firstrighttop @firstchangechoice="changefirstpage"/>
      <firstrightfirst v-if="firstpage[0]" :alldata="props.alldata"/>
      <firstrightsecond v-if="firstpage[1]" :alldata="props.alldata"/>
      <firstrightthird v-if="firstpage[2]" :alldata="props.alldata"/>
    </div>


  </div>
</template>

<script>
import firstlefttop from './firstleft/firstlefttop.vue';
import firstleftmain from './firstleft/firstleftmain.vue';
import firstrighttop from './firstright/firstrighttop.vue';
import firstrightfirst from './firstright/firstrightfirst.vue';
import firstrightsecond from './firstright/firstrightsecond.vue';
import firstrightthird from './firstright/firstrightthird.vue';

import{ref,reactive}from 'vue'
export default {
  components:{
    firstlefttop,
    firstleftmain,
    firstrighttop,
    firstrightfirst,
    firstrightsecond,
    firstrightthird
  },
  props:['alldata','actionhistory'],
  emit:['firstchangechoice'],
  setup(props,{emit}){
    var choice=reactive([true,false,false])
    function changechoice(n){
      choice.forEach((el,index)=>{
        choice[index]=false
      })
      choice[n]=true
      
    }
    var firstpage=reactive([true,false,false])
    function changefirstpage(n){
      firstpage.forEach((el,index)=>{
        firstpage[index]=false
      })
      firstpage[n]=true
    }

    return{
      changechoice,
      choice,
      changefirstpage,
      firstpage,
      props
    }
  }
}
</script>

<style scoped>


#first{
  width: 100%;
  height: 100%;
  display: flex;
  
  justify-content: space-evenly;
  
}
#mainleft{
  width: 25%;
  height: 100%;
  opacity: 1;
  background-color: #ffffff9e;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  transform: border-radius 0.1s ease;
  
}
#mainleft:hover{
  background-color: #c9ffe6;
  border-radius: 10px;
  transform-style: preserve-3d;
  transform: translate(0,-5%);
  box-shadow: 0px 0 10px 0#3e3e3e;
}
#mainright{
  width: 70%;
  height: 100%;
}
@keyframes float {

  0%,
  100% {
    transform: translateY(0) rotateX(1deg) rotateY(1deg);
  }

  50% {
    transform: translateY(-15px) rotateX(-1deg) rotateY(-1deg);
  }
}
#firstrighttop{
  width: 40%;
  height: 10%;
  transform: translate(0,-100%);
  display: flex;
}
.topchoose{
  width: 32%;
  height: 100%;
  margin-right: 1%;
  border-radius: 0 50% 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  transition: transform 0.2s ease;
  text-decoration: none;
  background-color: #b3ffac;
  color:rgb(51, 0, 255);
}
.topchoose:hover{
  background-color: #a1ffe7;
  cursor: pointer;
  box-shadow: 0 0 10px 0 black;
  transform: translate(0,20%);
  color:rgb(181, 10, 244)
}
.topchoose:active{
  background-color: #98f9c1;
  box-shadow: inset 0 0 5px 0 black;
}
.isactive{
  background-color: #34fecc;
  box-shadow: 0 0 10px 0 black;
  transform: translate(0,20%);
}
</style>