<template>
  <div id="secondall">
    <secondleft/>
    <secondmiddle v-if="middleappear" :target="goal"/>
    <secondre v-if="reappear" :target="goal" :res="result"/>
  </div>
</template>

<script>
import secondleft from './secondleft.vue';
import secondmiddle from './secondmiddle.vue';
import secondre from './secondre.vue';
import { eventBus } from '@/eventbus';
import{ref,reactive,onMounted,onUnmounted}from 'vue'
export default {
  components:{
    secondleft,
    secondmiddle,
    secondre
  },
  setup(){ 
    var result=ref(null)
    var goal=ref(0)
    var middleappear=ref(false)
    var reappear=ref(false)
    function startmain(n){
      middleappear.value=false
      setTimeout(() => {
        middleappear.value=true
      }, 0);
      goal.value=n
    }
    function closemain(man){
      middleappear.value=man
      reappear.value=man
    }
    function showresult(man)
    {
      console.log(man)
      result.value=man.result
      setTimeout(() => {
        reappear.value=man.isshow
      }, 1);
    }
    function closere(man){
      reappear.value=man
    }
    onMounted(() => {
      eventBus.on('startsecond', startmain);
      eventBus.on('closesecondmain',closemain)
      eventBus.on('showit',showresult)
      eventBus.on('closere',closere)
      
    });
    onUnmounted(() => {
      eventBus.off('startsecond', startmain);
      eventBus.off('closesecondmain',closemain)
      eventBus.off('showit',showresult)
    });
    
    


    return{
      middleappear,
      startmain,
      goal,
      reappear,
      result
    }
  }
}
</script>

<style scoped>

#secondall{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

}

</style>