<template>
  <div id="fifthshowright">
    <div id="protect" v-if="protection">
      调集数据中...
    </div>
    
    <div id="picshow" :class="{'animate__slideInLeft animate__animated':dateanimate}">
      <div id="pic">
        <img :src="imgurl" class="myimg" v-if="haveimg">
        <div class="protected" v-if="!protection&&!haveimg">
          当天尚未记录图片<br>或服务器未响应
        </div>

      </div>
      <div id="piclist">
        <div id="chartdiv">
          <canvas ref="selfchart" v-if="havedata"></canvas>
        </div>
        <div class="protected" v-if="!havedata&&!protection">
          当天尚未记录数据<br>或服务器未响应
        </div>
      </div>
    </div>

    <div id="textandaudio" :class="{'animate__slideInLeft animate__animated':dateanimate}">
      <div id="text">

        <div id="texttitle" @click="console.log(`文本${havetext},音频：${haveaudio},图片：${haveimg},数据：${havedata}`)">
          {{ year }}.{{ month }}.{{ day }}的过往
        </div>
        <hr>
          <div id="textmain">
            
            <div id="textinner" v-html="dailytext"></div>
            <div class="protected" v-if="!havetext&&!protection">
            当天尚未记录文字<br>或服务器未响应
          </div>
        </div>

      </div>
      <div id="audio" @click="showdetail">
        <h1 v-if="haveaudio" id="audiotitle">那时的回音</h1>
        <audio :src="soundurl" controls v-if="haveaudio"></audio>
        <div class="protected" v-if="!haveaudio&&!protection">
          当天尚未记录音频<br>或服务器未响应
        </div>
      </div>


    </div>


  </div>
</template>

<script>
import { eventBus } from '@/eventbus';
import { reactive, ref,onMounted,onUnmounted, watch, nextTick } from 'vue';
import axios from 'axios'
import Chart from 'chart.js/auto';
export default {
  props:['alldata','date'],
  setup(props)
  {
    var haveimg=ref(false)
    var dailytext=ref('')
    var havetext=ref(false)
    var havedata=ref(false)
    var haveaudio=ref(false)
    var year=ref(props.date[0])
    var month=ref(props.date[1])
    var day=ref(props.date[2])
    var imgurl=ref(null)
    var soundurl=ref(null)
    let selfchart=ref(null)
    var selfdata=reactive([0,0,0,0,0,0,0])
    let selfchartself=null
    var rightyear=ref(props.date[0])
    var rightmonth=ref(props.date[1])
    var rightday=ref(props.date[2])
    var truetext=ref('')
    
    



    function changedailytext(year,month,day){
      if(props.alldata[year]){
      if(props.alldata[year][month-1]){
        if(props.alldata[year][month-1][day]){
          dailytext.value=props.alldata[year][month-1][day].diary
          truetext.value=dailytext.value.replace(/<[^>]*>/g, '').trim()
          havetext.value=((truetext.value)!=='')
          }
        }
      }
    }


   async function wehavedata() {
      // console.log(rightyear.value,rightmonth.value,rightday.value)
      var mooddata=[0,0,0,0,0,0,0]
      if(props.alldata[rightyear.value][rightmonth.value-1][rightday.value]!==undefined)mooddata=props.alldata[rightyear.value][rightmonth.value-1][rightday.value].mood
      // console.log("年",rightyear.value,"月",rightmonth.value,"日",rightday.value)
      // console.log("最好是undefined",mooddata)
      if (!Array.isArray(mooddata) || mooddata.length !== 7) {
        console.error("数据格式错误", mooddata);
        return;
      }
      havedata.value=true
      for (var i = 0; i < selfdata.length; i++) {
        selfdata[i] = mooddata[i];
      }
      // console.log("日期的",mooddata)
      // console.log("这里的", selfdata);

      await nextTick(); // 确保 DOM 更新完成
      if (selfchart.value && havedata.value) {
        const ctx = selfchart.value.getContext("2d");
        if (selfchartself) {
          selfchartself.destroy();
        }
        selfchartself = new Chart(ctx, {
          type: "bar",
          data: {
            labels: ["喜悦", "悲伤", "愤怒", "惊讶", "厌恶", "恐惧", "复杂"],
            datasets: [
              {
                label: "情感得分",
                data: selfdata,
                borderColor: "rgba(75, 192, 192, 0.8)",
                backgroundColor: ['#228B22', '#191970', '#DC143C', '#9932CC', '#A0522D','#DCDCDC','#B0C4DE'],
                borderWidth: 1,
              },
            ],
          },
          options: {
            scales: {
              y: {
                beginAtZero: true,
                ticks: {
                  stepSize: 10,
                },
              },
            },
            plugins: {
              legend: {
                display: true,
              },
            },
          },
        });
      }
    }
    




    function changedailytexttwo(man)
    {
      year.value=man.year
      month.value=man.month
      day.value=man.day
      changedailytext(year.value,month.value,day.value)
    }
    watch([year,month,day],([newyear,newmonth,newday],[oldyear,oldmonth,oldday])=>{
      changedailytext(year.value,month.value,day.value)
      havetext.value=((truetext.value)!=='')
    },{immediate:true,deep:true})
    
    async function getimg(year,month,day){
      var monthzero=''
      var dayzero=''
      if(month<10)monthzero='0'
      if(day<10)dayzero='0'
      var imgname=`${year}${monthzero}${month}${dayzero}${day}`
      try{
        const response=await axios.post("/api/getimg",{date:imgname},{responseType:'blob'})
         if (response.data.type === "application/json") {
          const text = await response.data.text();
          const json = JSON.parse(text);
          if (!json.success) {
            haveimg.value = false;
            return;
          }
        }
        haveimg.value = true
        const blob = response.data;
        imgurl.value = URL.createObjectURL(blob);
      }catch (error) {
        console.error("请求出错:", error);
        haveimg.value=false
      }
      
    }
    async function getsound(year,month,day){
      console.log("准确年",year,month,day)
      rightyear.value=year
      rightmonth.value=month
      rightday.value=day
      var monthzero=''
      var dayzero=''
      if(month<10)monthzero='0'
      if(day<10)dayzero='0'
      var soundname=`${year}${monthzero}${month}${dayzero}${day}`
      try{
        const response=await axios.post("/api/getsound",{date:soundname},{responseType:'blob'})
         if (response.data.type === "application/json") {
          const text = await response.data.text();
          const json = JSON.parse(text);
          if (!json.success) {
            haveaudio.value = false;
            return;
          }
        }
        haveaudio.value = true
        const blob = response.data;
        soundurl.value = URL.createObjectURL(blob);
      }catch (error) {
        console.error("请求出错:", error);
        haveaudio.value = false;
      }
      
    }
    function letusagain(){
      getimg(props.date[0],props.date[1],props.date[2])
      getsound(props.date[0],props.date[1],props.date[2])
      wehavedata()
    }
    function cleartodaytext(man)
    {
      // console.log("今天的数据",man)
      // var year=man.year
      // var month=man.month
      // var day=man.day
      
      // console.log("全部的数据",props.alldata,"年",props.alldata[year],"月",props.alldata[year][month],"日",props.alldata[year][month][day])
      // console.log("其日记内容",props.alldata[year][month][day].diary==="")
      
      dailytext.value=""
      
      // props.alldata[year][day][month].diary=""
    }
    letusagain()
    

    







    var dateanimate=ref(false)
    var protection=ref(false)
    function animated()
    {
      dateanimate.value=true
      protection.value=true
      setTimeout(() => {
        dateanimate.value=false
        protection.value=false
      }, 1051);
    }
    function changetitle(man)
    {
      
      year.value=man.year
      month.value=man.month+1
      day.value=man.day
      imgurl.value=null
      soundurl.value=null
      getimg(year.value,month.value,day.value)
      getsound(year.value,month.value,day.value)
    }
    animated()

    function showdetail(){
      console.log("保护的值",protection.value)
      console.log("文本",havetext.value,'音频',haveaudio.value,'图片',haveimg.value,'数据',havedata.value)
      console.log('音频保护综合',!haveaudio.value&&!protection.value,'图片保护综合',!haveimg.value&&!protection.value)
    }
    



    onMounted(()=>{
      eventBus.on("datechange",animated)
      eventBus.on('datechange',changetitle)
      eventBus.on('datechange',wehavedata)
      eventBus.on('totoday',changedailytexttwo)
      eventBus.on('wehavedata',wehavedata)
      eventBus.on('reload',letusagain)
      eventBus.on('cleartodaytext',cleartodaytext)
    })
    onUnmounted(()=>{
      eventBus.off("datechange",animated,changetitle)
      eventBus.off('totoday',changedailytexttwo)
      eventBus.off('wehavedata',wehavedata)
      eventBus.off('reload',letusagain)
      eventBus.off('cleartodaytext',cleartodaytext)
    })
    havedata.value=(havetext.value||haveimg.value||haveaudio.value)


    return{
      dateanimate,
      protection,
      haveimg,
      haveaudio,
      havetext,
      havedata,
      year,
      month,
      day,
      dailytext,
      imgurl,
      soundurl,
      selfchart,
      showdetail
    }
  }
}
</script>

<style scoped>
 
.protected{
  width: 100%;
  height: 100%;
  background-color: #0000004c;
  color:white;
  font-size: 28px;
  font-weight: 589;
  font-family:'KaiTi';
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
}
.protected:hover{
  background-color: #f8f8f8aa;
  color: black;
  font-size: 35px;
}
#fifthshowright{
  width: 70%;
  height: 90%;
  display: flex;
  justify-content: space-evenly;
  position: relative;
  border: 1px solid black;
}
#picshow{
  width: 55%;
  height: 100%;
  
  border: 1px solid black;
}
#pic{
  width: 100%;
  height: 65%;
  border: 1px solid black;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
#piclist{
  width: 100%;
  height: 35%;
  border: 1px solid black;
  display: flex;
  position: relative;
  flex-wrap: wrap;
}
#textandaudio{
  border: 1px solid black;
  width: 40%;
  height: 100%;
}
#text{
  width: 100%;
  height: 70%;
  border: 1px solid black;
  position: relative;
}
#audio{
  width: 100%;
  height: 30%;
  position: relative;
  display: flex;
  justify-content: center;
  border: 1px solid black;
  align-items: center;
}
#texttitle{
  width: 80%;
  height: 24%;
  margin:0 auto;
  margin-bottom: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 600;
  font-family: 'KaiTi';
}
#textmain{
  width: 95%;
  height: 74%;
  margin:0 auto;
  margin-top: 1%;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
#textmain:hover{
  background-color: #ffd3d3e2;
  box-shadow: inset 0 0 15px blueviolet;
}
#protect{
  width: 100%;
  height: 100%;
  background-color: #00000090;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  font-weight: 650;
  font-family: 'KaiTi';
  position: absolute;
  left:0;
  right:0;
}
#textinner{
  width: 95%;
  height: 95%;
  font-size: 20px;
  font-weight: 580;
  font-family: 'KaiTi';
}
.myimg{
  height: 100%;
  aspect-ratio: 1/1;
  margin:0 auto;
}
#audiotitle{
  position: absolute;
  width: 80%;
  height: 24%;
  left:10%;
  display: flex;
  justify-content: center;
  align-items: center;
  top:5%;
}
#chartdiv{
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>