<template>
  <div id="secondresult">
    <canvas ref="chartCanvas"></canvas>
    <br><hr>
    <div id="secondpicsay" v-if="resultText">
      识别的结果为：
      <h1>{{ resultText }}</h1>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import { Chart } from 'chart.js/auto'
import { eventBus } from '@/eventbus'
export default {
  props:['result'],
  setup(props) {
    const nowaday=new Date()
    const resultText = ref('')
    const labels = ["快乐", "悲伤", "愤怒", "惊喜", "厌恶", "恐惧", "复杂"]
    const textdatachange={
      '快乐':0,
      '悲伤':1,
      '愤怒':2,
      '惊喜':3,
      '厌恶':4,
      '恐惧':5,
      '复杂':6
    }
    var picdata = [
        props.result.data.joy,
        props.result.data.sadness,
        props.result.data.anger,
        props.result.data.surprise,
        props.result.data.disgust,
        props.result.data.fear,
        props.result.data.others
      ]
      picdata.forEach((el,index)=>{
        picdata[index]=Number(picdata[index])
      })
    function mainmood(ans){
      var mainre=""
      var mainrenum=0
      ans.forEach((el,index)=>{
        if(ans[index]>mainrenum)
        {
          mainre=labels[index]
          mainrenum=ans[index]
        }
      })
      console.log("res",mainre)
      return mainre
    }
    resultText.value = mainmood(picdata)
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:`<p class=actionmain>您使用了文本识别，识别结果是<strong>${resultText.value}</strong></p>`})
    
    const chartCanvas = ref(null)
    let chartInstance = null
    

    
    async function showtextre(res) {
      await nextTick()

      if (!chartCanvas.value) {
        console.error('canvas 还没挂载')
        return
      }
      if (chartInstance) {
        chartInstance.destroy()
      }

      

      const ctx = chartCanvas.value.getContext('2d')
      chartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels,
          datasets: [{
            label: '情感分析结果',
            data: picdata,
            backgroundColor: [
              'rgb(0,255,127)',
              'rgb(0,255,255)',
              'rgb(220,20,60)',
              'rgb(255,215,0)',
              'rgb(128,128,0)',
              'rgb(25,25,112)',
              'rgb(138,43,226)'
            ],
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              ticks: {
                callback: function (value) {
                  return value.toFixed(0) + '%'
                }
              }
            }
          }
        }
      })
    //   var result={
    //     'joy': '喜悦😄',
    //     'sadness': '悲伤😢',
    //     'anger': '愤怒😠',
    //     'fear': '恐惧😨',
    //     'disgust': '厌恶🤢',
    //     'surprise': '惊喜😮',
    //     'others': '其他😐'
    // }
    
    // console.log("整体",textdatachange[resultText.value.trim()],"对象",textdatachange,'索引',resultText.value.trim())
    eventBus.emit('changemoodhistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:textdatachange[resultText.value.trim()],time:1})
    }
    eventBus.emit('actionhistory',{action:`<p class=actionmain>您刚刚使用了文本识别，识别结果是<strong>${resultText.value}</strong></p>`})
    showtextre(props.result)
    

    onMounted(() => {

    })

    onUnmounted(() => {
      if (chartInstance) {
        chartInstance.destroy()
      }
    })

    return {
      resultText,
      chartCanvas
    }
  }
}
</script>

<style scoped>

#secondresult{
  width: 100%;
  height: 100%;
  background-color: #ffffff82;
}
#secondresult:hover{
  background-color: #ffffffc6;
}
#secondpicsay{
  width: 90%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-family: 'KaiTi';
  margin:0 auto;
}
</style>