<template>
  <div id="forthall">
    <div id="protect" v-if="showprotect">正在加载...</div>
    <forthtop/>
    <forthbottom/>
  </div>
</template>

<script>
import forthtop from './forthtop.vue';
import forthbottom from './forthbottom.vue';
import { eventBus } from '@/eventbus';
import { ref,reactive,onMounted,onUnmounted} from 'vue';
export default {
  components:{
    forthtop,
    forthbottom
  },
  setup(){
    var showprotect=ref(false)
    function changeshow(man)
    {
      showprotect.value=man
      setTimeout(() => {
        showprotect.value=false
      }, 1050);
    }
    onMounted(()=>{
      eventBus.on('topchange',changeshow)
    })
    onUnmounted(()=>{
      eventBus.off('topchange',changeshow)
    })



    return{
      showprotect
    }
  }
}
</script>

<style scoped>

#forthall{
  position: relative;
  width: 100%;
  height: 100%;
}
#protect{
  width: 100%;
  height: 100%;
  position: absolute;
  left:0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  font-weight: 580;
  background-color: #33333335;
  color:white;
  font-family: 'KaiTi';
  z-index: 180;
  opacity: 1;
}

</style>