<template>
  <div id="bottomleft" class="animate__fadeInUp animate__animated">
    <!-- 歌曲封面 -->
    <div id="leftimg" :class="{'animate__fadeInUp animate__animated':animation}">
      <img :src="mainimg" id="mainimg" :class="{'animate__fadeInUp animate__animated':animation}">
    </div>

    <!-- 歌名 -->
    <div id="leftsaying" :class="{'animate__fadeInUp animate__animated':animation}">
      {{ musicname[musicpagenum] }}
    </div>

    <!-- 进度条（可点击 / 拖动） -->
    <div id="musictime" 
         ref="progressBar"
         :class="{'animate__fadeInUp animate__animated':animation}"
         @click="seekMusic($event)"
         @mousedown="startDrag($event)">
      <div id="realtime"
           :style="{ width: timeratio + '%', height:'100%', backgroundColor:'skyblue'}"
           :class="{'animate__fadeInUp animate__animated':animation}">
      </div>
    </div>

    <!-- 工具栏 -->
    <div id="musicbar" :class="{'animate__fadeInUp animate__animated':animation}">
      <div class="muscibartool" :style="{backgroundImage:'url(/src/19.png)'}" @click="changemusic(0)"></div>
      <div class="muscibartool" 
           :style="{backgroundImage:`url(${radio})`}" 
           @click="changeradio"></div>
      <div class="muscibartool" :style="{backgroundImage:'url(/src/20.png)'}" @click="changemusic(1)"></div>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus';
import 'animate.css'
import { ref, onMounted, onUnmounted,reactive } from 'vue';

export default {
  setup() {
    const animation = ref(false)
    const mainimg = ref('/src/28.jpg')
    var musicname = reactive([
      '','','','','','','',''
    ])
    var musicpagenum=ref(0)


    const radio = ref('/src/18.png')
    const timeratio = ref(0)
    const isplay = ref(false)

    let music = null
    let rafId = null
    let isDragging = false
    const progressBar = ref(null)

    function stopMusic() {
      if (music) {
        // 停止播放
        music.pause()
        music.currentTime = 0
      }

      // 状态复位
      isplay.value = false
      timeratio.value = 0
      radio.value = '/src/18.png' // 切回未播放图标

      // 停掉 requestAnimationFrame
      if (rafId) {
        cancelAnimationFrame(rafId)
        rafId = null
      }
    }
    /*当切换键被点下的时候 */
    function changemusic(n){
      if(musicname[musicpagenum]===undefined){
        eventBus.emit('information','请先选择乐曲！')
        return
      }
      eventBus.emit('samechange',n)
      // eventBus.emit('action',{action:"<p id=actionmain>您使用音乐调频并聆听了<strong>"+musicname[musicpagenum]+"</strong></p>"})
    }

    /** 动画开关 */
    function onanimate(man) {
      musicname[man.nownum]=realname(man.nowname)
      musicpagenum.value=man.nownum
      animation.value = man.isit
      setTimeout(() => {
        animation.value = false
      }, 1051);
      stopMusic()
    }

    /** 播放按钮切换 */
    function changeradio() {
      if (!music) return
      if (music.paused) {
        music.play()
        radio.value='/src/17.png'
      } else {
        music.pause()
        radio.value='/src/18.png'
      }
    }

    /** 文件名去掉后缀 */
    function realname(str) {
      return str.replace(/\.[^/.]+$/, '')
    }

    /** 设置封面图 */
    function changmainimg(value) {
      mainimg.value = value
    }
    



    /** 设置音乐名字 */
    function onmusic(k) {
      musicname[musicpagenum.value]=realname(k.name)
    }

    /** 播放进度渲染 (rAF) */
    function updateProgress() {
      if (music && !music.paused && music.duration && !isDragging) {
        timeratio.value = (music.currentTime / music.duration) * 100
        rafId = requestAnimationFrame(updateProgress)
      }
    }

    /** 点击进度条跳转 */
    function seekMusic(event) {
      if (!music || !progressBar.value) return
      const rect = progressBar.value.getBoundingClientRect()
      const clickX = event.clientX - rect.left
      const percent = Math.min(Math.max(clickX / rect.width, 0), 1)
      music.currentTime = percent * music.duration
      timeratio.value = percent * 100
    }

    /** 开始拖动 */
    function startDrag(event) {
      if (!music || !progressBar.value) return
      isDragging = true
      moveDrag(event)

      const onMouseMove = (e) => moveDrag(e)
      const onMouseUp = (e) => {
        moveDrag(e)
        isDragging = false
        document.removeEventListener('mousemove', onMouseMove)
        document.removeEventListener('mouseup', onMouseUp)
        // 拖动结束后，直接修改音乐播放时间
        const rect = progressBar.value.getBoundingClientRect()
        const pos = Math.min(Math.max(e.clientX - rect.left, 0), rect.width)
        const percent = pos / rect.width
        music.currentTime = percent * music.duration
      }

      document.addEventListener('mousemove', onMouseMove)
      document.addEventListener('mouseup', onMouseUp)
    }

    /** 拖动过程中实时更新 UI */
    function moveDrag(e) {
      if (!progressBar.value) return
      const rect = progressBar.value.getBoundingClientRect()
      const pos = Math.min(Math.max(e.clientX - rect.left, 0), rect.width)
      const percent = pos / rect.width
      timeratio.value = percent * 100
    }

    /** 接收 music 对象并播放 */
    function handmusicplay(audio) {
      music = audio
      isplay.value = true
      radio.value = '/src/17.png'

      music.play()
      updateProgress()

      // 精度控制
      music.addEventListener('timeupdate', () => {
        if (music.duration && !isDragging) {
          timeratio.value = (music.currentTime / music.duration) * 100
        }
      })

      // 播放结束
      music.addEventListener('ended', () => {
        isplay.value = false
        radio.value = '/src/18.png'
        timeratio.value = 0
        cancelAnimationFrame(rafId)
      })
    }

    onMounted(() => {
      eventBus.on('musicimg', changmainimg)
      eventBus.on('onmusic', onmusic)
      eventBus.on('topchange', onanimate)
      eventBus.on('musicplay', handmusicplay)
      eventBus.on('samepagechange',stopMusic)
    })

    onUnmounted(() => {
      eventBus.off('musicimg', changmainimg)
      eventBus.off('onmusic', onmusic)
      eventBus.off('topchange', onanimate)
      eventBus.off('musicplay', handmusicplay)
      eventBus.off('samepagechange',stopMusic)
      cancelAnimationFrame(rafId)
    })

    return {
      mainimg,
      musicname,
      changeradio,
      radio,
      timeratio,
      animation,
      isplay,
      progressBar,
      seekMusic,
      startDrag,
      musicpagenum,
      changemusic
    }
  }
}
</script>


<style scoped>
#bottomleft{
  width: 40%;
  height: 80%;

}
#leftimg{
  width: 80%;
  height: 40%;
  margin:1% auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
#mainimg{
  height: 80%;
  aspect-ratio: 1/1;
}
#leftsaying{
  width: 60%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 560;
  font-family: 'KaiTi';
  margin:0 auto;
}

#musictime{
  width: 85%;
  height: 5%;
  background-color: #8b8b8ba1;
  margin:2% auto;
}
#musictime{
  cursor: pointer;
}
#realtime:hover{
  border: 1px solid black;
  box-shadow: 0 0 7px 0 rgb(254, 30, 247);
}

#musicbar{
  width: 90%;
  height: 23%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin:0 auto;
}
.muscibartool{
  height: 80%;
  aspect-ratio: 1/1;
  transition: background-color 0.1s ease;
}
.muscibartool:hover{
  box-shadow: 0 0 5px 0 black;
  background-color: #ffffff9f;
  border-radius: 10px;
  cursor: pointer;
}
.muscibartool:active{
  background-color: #525252;
}
</style>