<template>
  <div id="firstall">
    <div id="protect" v-if="animatefalse">
      正在加载中...
      </div>
    <div id="firsttop">
      <div id="firstchartfirst">
        <canvas ref="chartCanvas"></canvas>
      </div>
      <ul>
        <li :class="{'animate__fadeInLeft animate__animated animate__bounce animate__delay-0.2s':animatefalse}">
          <img src="/src/3.png" alt="" class="firsttoppic">
          <p>累计使用了{{historysum[0]}}次情绪识别</p>
        </li>
        <li :class="{'animate__fadeInRight animate__animated animate__bounce animate__delay-0.4s':animatefalse}">
          <img src="/src/4.png" alt="" class="firsttoppic">
          <p>累计使用了{{historysum[1]}}次情绪调频</p>
        </li>
        <li :class="{'animate__fadeInUp animate__animated animate__bounce animate__delay-0.6s':animatefalse}">
          <img src="/src/5.png" alt="" class="firsttoppic">
          <p>累计使用了{{historysum[2]}}次音乐疗愈</p>
        </li>
        <li :class="{'animate__fadeInDown animate__animated animate__bounce animate__delay-0.8s':animatefalse}">
          <img src="/src/6.png" alt="" class="firsttoppic">
          <p>累计记录了{{historysum[3]}}次情绪日志</p>
        </li>
      </ul>
    </div>
    <div id="firstchartsecond">
      <div id="firstpiechart">
        <canvas ref="chartPie"></canvas>
      </div>
      <div id="firsthistorychart">
        <canvas ref="chartHistory"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, nextTick,reactive } from 'vue';
import Chart from 'chart.js/auto';
import 'animate.css'
export default {
  props:['alldata'],
  setup(props) {
    const nowaday=new Date()
    var animatefalse=ref(true)
    setTimeout(() => {
      animatefalse.value=false
    }, 1050);
    const chartCanvas = ref(null);
    const chartPie = ref(null);
    const chartHistory = ref(null);

    let firstchartfirst = null;
    let firstchartpie = null;
    let firstcharthistory = null;
    var indicatorsum=reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].indicator.length).fill(0))
    var moodsum=reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].mood.length).fill(0))
    var moodhistorysum=reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].moodhistory.length).fill(0))
    var historysum=reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].history.length).fill(0))
    function indicatorok(){
      var totalindicator=0
      for(var year in props.alldata)
      {
        for(var month in props.alldata[year])
        {
          for(var day in props.alldata[year][month]){
            const dailyindicators=props.alldata[year][month][day].indicator
            dailyindicators.forEach((value,index)=>{
              indicatorsum[index]+=value
            })
            totalindicator++
          }
        }
      }
      indicatorsum.forEach((el,index)=>{
        indicatorsum[index]/=totalindicator
      })
  }
  function moodok()
  {
    var totalmood=0

    for(var year in props.alldata)
    {
      for(var month in props.alldata[year])
      {
        for(var day in props.alldata[year][month]){
          const dailymoods=props.alldata[year][month][day].mood
          dailymoods.forEach((value,index)=>{
            moodsum[index]+=value
          })
          totalmood++
        }
      }
    }
    moodsum.forEach((el,index)=>{
      moodsum[index]/=totalmood
    })
  }
    moodok()

    indicatorok()
    function moodhistoryok() {
      var totalmoodhistory = 0
      for (var year in props.alldata) {
        for (var month in props.alldata[year]) {
          for (var day in props.alldata[year][month]) {
            const dailymoodhistory = props.alldata[year][month][day].moodhistory
            dailymoodhistory.forEach((value, index) => {
              moodhistorysum[index] += value
            })
            totalmoodhistory++
          }
        }
      }
      // moodhistorysum.forEach((el, index) => {
      //   moodhistorysum[index] /= totalmoodhistory
      // })
    }
    moodhistoryok()

    function historyok() {
      for (var year in props.alldata) {
        for (var month in props.alldata[year]) {
          for (var day in props.alldata[year][month]) {
            const dailyhistory = props.alldata[year][month][day].history
            dailyhistory.forEach((value, index) => {
              historysum[index] += value
            })
          }
        }
      }
    }
    historyok()















    onMounted(async () => {
      await nextTick();

      // --- 第一个柱状图 ---
      if (chartCanvas.value) {
        const ctx = chartCanvas.value.getContext('2d');
        firstchartfirst = new Chart(ctx, {
          type: 'bar',
          data: {
            labels: [
              '情绪正面程度',
              '情绪负面程度',
              '历史结果可信度',
              '情绪稳定程度'
            ],
            datasets: [{
              label: '评估值',
              data: indicatorsum,
              backgroundColor: ['#4CAF50', '#2196F3', '#FF9800', '#9C27B0'],
              borderRadius: 6,
              barThickness: 24
            }]
          },
          options: {
            responsive: true,
            indexAxis: 'y',
            plugins: {
              legend: { display: false },
              title: { display: true, text: '个人情绪指标图', font: { size: 18 } },
              tooltip: {
                callbacks: {
                  label: (context) => `${context.label}: ${context.raw} 分`
                }
              }
            },
            scales: {
              x: {
                min: 0, 
                title: { display: true, text: '得分数值' },
                ticks: { callback: (value) => value  }
              }
            }
          }
        });
      }

      // --- 第二个饼图 ---
      if (chartPie.value) {
        const ctx2 = chartPie.value.getContext('2d');
        const emotionDescriptions = {
          '喜悦': '那是时间留给生命的翩翩起舞。',
          '平静': '平常一般的平淡如水或许才是生活的真相。',
          '悲伤': '你哭了，我知道。',
          '愤怒': '很久很久以前，我也有如此的模样……',
          '复杂': '很少有纯粹的人，正如没有纯粹的情绪一样',
          '厌恶':'答应我，诀别的时候，不要这样……',
          '惊讶':'不期而遇的，是我们久别重逢的一见如故',
          '恐惧':'就像生如朝露，就像天暖人暮'
        };
        firstchartpie = new Chart(ctx2, {
          type: 'pie',
          data: {
            labels: ['喜悦', '悲伤', '愤怒', '惊讶', '厌恶','恐惧','复杂'],
            datasets: [{
              label: '本周情绪构成',
              data: moodsum,
              backgroundColor: ['#228B22', '#191970', '#DC143C', '#9932CC', '#A0522D','#DCDCDC','#B0C4DE'],
              borderWidth: 1
            }]
          },
          options: {
            responsive: true,
            plugins: {
              tooltip: {
                callbacks: {
                  label: (context) => {
                    const label = context.label;
                    const value = context.raw;
                    const description = emotionDescriptions[label] || '';
                    return `${label}: ${value}%\n${description}`;
                  }
                }
              },
              legend: { position: 'bottom' },
              title: { display: true, text: '历史情绪组成（百分比）', font: { size: 18 } }
            }
          }
        });
      }

      // --- 第三个柱状图 ---
      if (chartHistory.value) {
        const ctx3 = chartHistory.value.getContext('2d');
        firstcharthistory = new Chart(ctx3, {
          type: 'bar',
          data: {
            labels: ['喜悦', '悲伤', '愤怒', '惊讶', '厌恶','恐惧','复杂'],
            datasets: [{
              label: '识别次数',
              data: moodhistorysum,
              backgroundColor:['#228B22', '#191970', '#DC143C', '#9932CC', '#A0522D','#DCDCDC','#B0C4DE'],
              borderColor: [
                'black',
                'black',
                'black',
                'black',
                'black',
                'black',
                'black'
              ],
              borderWidth: 1
            }]
          },
          options: {
            responsive: true,
            scales: {
              y: { beginAtZero: true, title: { display: true, text: '次数' } },
              x: { title: { display: true, text: '情绪种类' } }
            },
            plugins: {
              title: { display: true, text: '历史情绪识别记录' },
              legend: { display: false }
            }
          }
        });
      }
    });

    return { chartCanvas, chartPie, chartHistory,animatefalse,historysum };
  }
};
</script>



<style scoped>
 
#protect{
  width: 100%;
  height: 100%;
  background-color: #73737356;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: 640;
  font-family: 'KaiTi';
  color:white;
  z-index:10;
  position: absolute;
  
}
#firstall{
  width: 100%;
  height: 100%;
  transform: translate(0,-10%);
  background-color: #ffffff61;
}
#firsttop{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;
}
#firstchartfirst{
  width: 60%;
  height: 100%;
}

ul{
  background-color: #ffffff77;
  width: 30%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;;
}
li{
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
li:hover{
  box-shadow: 0 0 3px 0 black;
  background-color: #51ffc5bd;
  animation:hoverup 0.2s ease
}
.firsttoppic{
  width: 18%;
  height: 80%;
}
p{
  font-size: 20px;
  font-weight: 550;
}
@keyframes hoverup {
  0%{
    transform: translate(0,0);
  }
  50%{
    transform: translate(0,-10%);
  }
  100%{
    transform: translate(0,0);
  }
}
#firstchartsecond{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;

}
#firstpiechart{
  width: 30%;
  height: 100%;
}
#firsthistorychart{
  width: 60%;
  height: 100%;
}
#firstchartfirst:hover,#firstpiechart:hover,#firsthistorychart:hover{
  background-color: #fff;
}
@keyframes setup {
  0%{
    transform: translate(0,20%);
    opacity: 0.2;
  }
  100%{
    transform: translate(0,0);
    opacity: 1;
  }
}
#firstchartfirst,#firstpiechart,#firsthistorychart{
  animation:setup 0.8s ease;
}
</style>