<template>
  <div id="secondmain">
    <button id="close" @click="closeseconmain">关闭</button>
    <div id="secondgo">
      <div id="secondsaying">
        <p class="saying">{{ character[props.target].first }}</p>
        <p class="saying">{{ character[props.target].second }}</p>
        <p class="saying">{{ character[props.target].third }}</p>
      </div>

      <div id="secondinput" style="background-image: url('/src/16.png');">
        <input type="file" id="trueinput" @change="setupfile">
        <div class="chosenfile">{{ chosenfile }}</div>
      </div>

      <div id="light">
        <div id="lightfirst">
          <div id="deng" :style="{'background-color':colorchoice}"></div>
          <p id="lightsay">{{ inner}}</p>
        </div>
        <button id="secondsubmit" class="animate__bounceIn animate_animated" v-if="submittime" 
        @click="submitted">提交</button>
        <button id="secondshowre" v-if="showre"
        class="animate__bounceIn animate_animated" @click="showit">显示结果</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {ref,reactive}from 'vue'
import { eventBus } from '@/eventbus'
export default {
  props:['target'],
  setup(props){
    function closeseconmain(){
      eventBus.emit("closesecondmain",false)
    }
    var inner=ref("文件待上传")
    var colorchoice=ref('red')
    var submittime=ref(false)
    var chosenfile=ref("未选择文件")
    var showre=ref(false)
    function Done()
    {
      inner.value="识别完成！"
      colorchoice.value="green"
      showre.value=true
    }
    function Error(){
      inner.value="后端服务错误！"
      colorchoice.value="red"
    }
    var real_file
    var result
    var character=[
      {
        first:'透过对字里行间的行文的分析串联起一个个无声的文字',
        second:'织成细腻而婉转的笔者的弦外之音',
        third:'从而剖析出，每一句所蕴含的情绪的意义',
      },
      {
        first:'计算机……冰冷的机器么，不，它们也有视觉',
        second:'有着我们给予它们的，关于图片和情绪的记忆',
        third:'通过这些记忆，再去读懂我们的图片，便能得出其中思绪……'
      },
      {
        first:'因能量而颤动并向外传送波纹的信息',
        second:'当计算机学会了这些信息承载的意义',
        third:'它们也能够以数据分析和数据库为我们去阅读这些频率'
      },
      {
        first:'我们当然看不到我们悲喜之际的脑电',
        second:'将其捕捉，交给计算机吧',
        third:'我们的系统能够很快地给出答案'
      }
    ]
    function setupfile(e){
      showre.value=false
      eventBus.emit('showit',false)
      if(e.target.files[0]){
        real_file=e.target.files[0]
        inner.value="文件待提交"
        colorchoice.value="yellow"
        submittime.value=true
        var filename=e.target.files[0].name
        chosenfile.value=`已选择：${filename}`
        eventBus.emit('information','上传成功！')
      }
      else{
        colorchoice.value="red"
        inner.value="文件上传错误！"
      }
    }
    function submitted(){
      console.log("当前上传的文件：",real_file)
      colorchoice.value="orange"
      inner.value="文件正在分析..."
      const formData=new FormData()
      
      if(real_file)
      {

        if(props.target===0)
          {
            formData.append('text_file',real_file)
            axios.post('/api/text', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          }).then(res=>{
            result=res
            eventBus.emit('information','识别成功！')
            Done()
            
          }).catch(err=>{
            console.log(err)
            Error()
          })
          }
        else if(props.target===1){
          formData.append('pic_file',real_file)
          axios.post('/api/pic', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          }).then(res=>{
            result=res
            eventBus.emit('information','识别成功！')
            Done()
            
          }).catch(err=>{
            console.log(err)
            Error()
          })
        }
        else if(props.target===2){
          formData.append('sound_file',real_file)
          axios.post('/api/sound', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          }).then(res=>{
            result=res
            console.log(res.data)
            eventBus.emit('information','识别成功！')
            Done()
            
          }).catch(err=>{
            console.log(err)
            Error()
          })
        }
        else if(props.target===3){
          formData.append('brain_file',real_file)
          axios.post('/api/brain', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          }).then(res=>{
            result=res
            eventBus.emit('information','识别成功！（脑电结果不计入统计）')
            Done()
            
          }).catch(err=>{
            console.log(err)
            Error()
          })
        }

      }
      else{
        colorchoice.value="red"
        inner.value="文件有错误！"
      }
      
    }

    function showit(){
      eventBus.emit('showit',{isshow:true,result:result,id:props.target})
    }
    return{
      closeseconmain,
      character,
      props,
      inner,
      colorchoice,
      setupfile,
      submittime,
      submitted,
      chosenfile,
      showre,
      showit
    }
  }
}
</script>

<style scoped>
@keyframes turnout {
  0%{
    transform: translate(-30%,0);
    opacity: 0;
  }
  100%{
    transform: translate(0,0);
    opacity: 1;
  }
}

#secondmain{
  width: 50%;
  height: 90%;
  background-color: #ffffff8a;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: turnout 0.5s ease-in;
}
#close{
  width: 10%;
  height: 10%;
  border-radius: 10px;
  background-color: #67c7ff;
  font-size: 20px;
  font-weight: 580;
  position:absolute;
  right:1%;
  top:1%

}
#close:hover{
  background-color: #2b6cf9;
  cursor: pointer;
}
#secondgo{
  width: 90%;
  height: 85%;

}
#secondsaying{
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

}
.saying{
  width: 90%;
  height: 30%;
  margin:0 auto;
  font-size: 25px;
  font-weight: 620;
  font-family: 'KaiTi';
  display: flex;
  align-items: center;
}

#secondinput{
  width: 85%;
  height: 52%;
  margin:0 auto;
  position: relative;
  margin-top: 2%;
  background-repeat: no-repeat;
  background-size: 70% 100%;
  transition: border 0.1s ease,background-color 1s ease;
  background-position:center;
}
#secondinput:hover{
  background-color: #747272;
  border: 5px blue dashed;
}
#trueinput{
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}
#light{
  width: 90%;
  margin:0 auto;
  height: 13%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 1%;

}
#lightfirst{
  width: 32%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#deng{
  width: 10%;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
}
#lightsay{
  font-size: 25px;
  font-weight: 650;
  font-family: 'KaiTi';
}
#secondsubmit{
  width: 20%;
  height: 85%;
  border-radius: 15px;
  font-size: 28px;
  font-weight: 660;
  font-family: 'KaiTi';
  border: 1px solid black;
  background-color: #a0d3ff;
}
#secondsubmit:hover{
  background-color: #6da5ff;
  cursor: pointer;
}
.chosenfile{
  width: 25%;
  height: 18%;
  font-size: 18px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left:1%;
  bottom:0;
  font-weight: 520;
}
#secondshowre{
  width: 20%;
  height: 85%;
  border-radius: 10px;
  font-size: 22px;
  font-weight: 660;
  font-family: 'KaiTi';
  border: 1px solid black;
  background-color: #a0ffcb;
}
#secondshowre:hover{
  background-color: #6dff8d;
  cursor: pointer;
}
</style>