<template>
  <div style="border: 1px solid #ccc">
    <Toolbar
      style="border-bottom: 1px solid #ccc"
      :editor="editorRef"
      :defaultConfig="toolbarConfig"
      :mode="mode"
    />
    <Editor
      style="height: 341px; overflow-y: hidden;"
      v-model="valueHtml"
      :defaultConfig="editorConfig"
      :mode="mode"
      @onCreated="handleCreated"
    />
  </div>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css'
import { onBeforeUnmount, ref, shallowRef, onMounted, onUnmounted } from 'vue'
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import { eventBus } from '@/eventbus'

export default {
  components: { Editor, Toolbar },
  props: ['alldata', 'chosedate'],
  setup(props) {
    const nowaday = new Date()
    const ayear = ref(props.chosedate[0])
    const amonth = ref(props.chosedate[1])
    const adate = ref(props.chosedate[2])

    let latelytext = props.alldata[ayear.value][amonth.value][adate.value].diary || ''
    if (latelytext === '') {
      latelytext = '你好，世界'
    } 
    // else {
    //   if (latelytext.startsWith('<br>')) {
    //     latelytext = latelytext.substring(4)
    //   }
    // }

    // 编辑器实例
    const editorRef = shallowRef()

    // 内容 HTML，初始化必须是合法的 HTML
    const valueHtml = ref('')
    function cleanHtml(html) {
      return html.replace(/^<p>(<br>|&nbsp;|\s)*<\/p>/, '')
    }
    function updatediary() {
      eventBus.emit('diarychange', {
        year: ayear.value,
        month: amonth.value,
        day: adate.value,
        diary: valueHtml.value,
      })
    }

    onMounted(() => {
      // 延迟加载内容，使用 editor.setHtml 避免 slate 报错
      setTimeout(() => {
        if (editorRef.value) {
          editorRef.value.setHtml(`<p>${latelytext}</p>`)
        }
      }, 500)
      eventBus.on('savethis', updatediary)
    })

    onUnmounted(() => {
      eventBus.off('savethis', updatediary)
    })

    const toolbarConfig = {}
    const editorConfig = { placeholder: '请输入内容...' }

    // 组件销毁时销毁编辑器
    onBeforeUnmount(() => {
      const editor = editorRef.value
      if (editor == null) return
      editor.destroy()
    })

    const handleCreated = (editor) => {
      editorRef.value = editor // 记录 editor 实例
      if (latelytext) {
        editor.setHtml(cleanHtml(latelytext))
      }
    }

    return {
      editorRef,
      valueHtml,
      mode: 'default',
      toolbarConfig,
      editorConfig,
      handleCreated,
    }
  },
}
</script>

