<template>
  <div class="todo-container">
    <h1>今日待办事项</h1>
    <div class="todo-wrap">
        <top :receive="receive"></top>
        <list :todos="todos" :checktodo="checktodo" :removetodo="removetodo"></list>
    </div>
      <myfoot :todos="todos" :removetodo="removetodo" :select="select"></myfoot>
  </div>
</template>

<script>
import top from './todolistcomponents/top.vue';
import myfoot from './todolistcomponents/myfoot.vue';
import list from './todolistcomponents/list.vue';
import { eventBus } from '@/eventbus';
import {ref,reactive,watch, onMounted, nextTick}from 'vue'
export default {
  name: 'App',
  components: {
    top,
    myfoot,
    list
  },
  setup(){
    let todos = reactive(Array.isArray(JSON.parse(localStorage.getItem("todos"))) 
  ? JSON.parse(localStorage.getItem("todos")) 
  : [])
  /*定时清理 */
  function clearArray() {
    todos.length = 0; 
  }
  function getNextMidnightTime() {
    let now = new Date();
    let midnight = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1, 0, 0, 0);
    return midnight.getTime() - now.getTime();
  }
  function setMidnightTimer() {
    let delay = getNextMidnightTime();
    setTimeout(() => {
      clearArray();
      setMidnightTimer(); 
    }, delay);
  }
  setMidnightTimer();
  ////
    function receive(x){
      todos.unshift(x)
    }
    function checktodo(id){
      todos.forEach((todo)=>{
        if(todo.id===id)todo.done=!todo.done
      })
    }
    function removetodo(id){
      todos.forEach((el,index)=>{
        console.log(todos)
        if(el.id===id){
          todos.splice(index,1)
        }
      })
  }
    function select(a){
      if(a){
        todos.forEach((t)=>{
        t.done=true
        })
      }else{
        todos.forEach((t)=>{
        t.done=false
      })
      }
    }
    function editthings(newv){
      console.log(newv)
      todos.forEach((t)=>{
        if(t.id===newv.id){
          t.title=newv.value
        }
      })
    }
    onMounted(()=>{
      eventBus.on("edition",editthings)
    })
    watch(todos,(newv)=>{
      localStorage.setItem("todos",JSON.stringify(newv))
    },{deep:true,immediate:true})

    return{
      todos,
      receive,
      checktodo,
      removetodo,
      select,
      editthings
    }
  },


  
  }
</script>

<style scoped>
