<template>
  <div id="showall">
    <div id="showleft">
      <div id="showleftdate" class="animate__fadeIn animate__animated" @click="console.log(props.oldday[0],props.oldday[1],props.oldday[2],props.alldata,props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].diary)">
        <p id="year">{{ year }}年</p>
        <p id="monthday">{{ month }}月{{ day }}日</p>
      </div>

      <div id="chosedate" class="fifthtool animate__fadeInLeft animate__animated">
        <input type="date" id="thisdate" v-model="date" @change="changedate"  ref="dateInput" />
      </div>

      <div id="edition" class="fifthtool animate__fadeInLeft animate__animated" @click="intoedition">
        编辑日志
      </div>
      <div id="remove" class="fifthtool animate__fadeInLeft animate__animated" @click="removedata">
        删除日志
      </div>
    </div>

    <fifthshowright :alldata="props.alldata" :date="simpledate"/>
    
  </div>
</template>

<script>
import { reactive, ref } from 'vue';
import fifthshowright from './fifthshowright.vue';
import { eventBus } from '@/eventbus';
import axios from 'axios'
export default {
  components:{
    fifthshowright
  },
  props:['alldata','oldday'],
  setup(props) {
    const today = new Date();
    if(props.oldday.length){
      var year=ref(props.oldday[0])
      var  month=ref(props.oldday[1]+1)
      var  day=ref(props.oldday[2])
    }else{
      var year = ref(today.getFullYear());
      var month = ref(today.getMonth() + 1);
      var day = ref(today.getDate());
      props.oldday[0]=year.value
      props.oldday[1]=month.value-1
      props.oldday[2]=day.value
    }
    
    /*这一块要改，不然每次挂载都要触发一遍 */
    var simpledate=reactive([year.value,month.value,day.value])

    const date = ref(null);
    const dateInput = ref(null); 

    const showDatePicker = () => {
      if (dateInput.value) {
        dateInput.value.click(); 
      }
    };

    const changedate = () => {
      if (date.value) {
        var today=new Date()
        const DATE = new Date(date.value);
        let DATEWithoutTime = new Date(DATE.getFullYear(), DATE.getMonth(), DATE.getDate());
        let todayWithoutTime = new Date(today.getFullYear(), today.getMonth(), today.getDate());
        if(DATEWithoutTime>todayWithoutTime){
          eventBus.emit("information",'不可选择超过今天的日期！');
          console.log(DATE)
          console.log(today)
          console.log("今日",today.getMonth(),today.getDate())
          year.value = today.getFullYear();
          month.value = today.getMonth() + 1; 
          day.value = today.getDate();
          eventBus.emit("totoday",{year:year.value,month:month.value,day:day.value})
          return
        }
        year.value = DATE.getFullYear();
        month.value = DATE.getMonth() + 1; 
        day.value = DATE.getDate();
      } else {
        eventBus.emit("information",'请选择日期！');
        return
      }
      eventBus.emit("datechange",{year:year.value,month:month.value-1,day:day.value})
    };

    function intoedition()
    {
      eventBus.emit("Edition",{no:false,date:[year.value,month.value-1,day.value]})
    }
    function removedata()
    {
      console.log(props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].diary)
      
      var monthzero=(props.oldday[1]+1<10)?'0':''
      var dayzero=(props.oldday[2]+1<10)?'0':''
      var data={
        file_name:`${props.oldday[0]}${monthzero}${props.oldday[1]+1}${dayzero}${props.oldday[2]}`
      }
      axios.post('/api/clear', data, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }).then(
        res=>{
          // console.log("==================")
          // console.log(res)
          // console.log("时间",props.oldday[0],props.oldday[1],props.oldday[2])
          // console.log("内容",props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].diary)
          // console.log("==================")
          props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].diary=""
          for(var i=0;i<props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].mood.length;i++){
            props.alldata[props.oldday[0]][props.oldday[1]][props.oldday[2]].mood[i]=0
          }
          eventBus.emit('changehistory',{year:props.oldday[0],month:props.oldday[1],day:props.oldday[2],where:3,time:1,
            action:`<p id=actionmain>您删除了${props.oldday[0]}年${props.oldday[1]+1}月${props.oldday[2]}日的<strong>情绪日志</strong></p>`
          })
          eventBus.emit('information','删除成功！')
          // location.reload();
          eventBus.emit('reload')
          eventBus.emit('cleartodaytext',{year:props.oldday[0],month:props.oldday[1],day:props.oldday[2]})
        }
      ).catch(err=>{
          console.log(err)
          eventBus.emit('information',"后端服务器错误！请重试！")
          eventBus.emit('action',{action:`<p id=actionmain>您删除${props.oldday[0]}年${props.oldday[1]+1}月${props.oldday[2]}日的<strong>情绪日志</strong>时出错了</p>`})
          return
        })
        
        
    }

    return {
      year,
      month,
      day,
      date,
      dateInput,
      props,
      simpledate,
      showDatePicker,
      changedate,
      intoedition,
      removedata
    };
  },
};
</script>

<style>
/* 可以添加一些样式 */
button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}
</style>

<style scoped>
 
#showall{
  
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: #ffffff85;
}
#showleft{
  border: 1px solid black;
  width: 22%;
  height: 88%;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
#showleftdate{
  border: 1px solid black;
  width: 82%;
  height: 28%;
  margin-top: 5%;

}
#thisdate{
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #ffffff73;
}
#year{
  width: 80%;
  height: 15%;
  margin: 5% auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 580;
}
#monthday{
  width: 60%;
  height: 50%;
  margin: 5% auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  font-weight: 660;
}
.fifthtool{
  width: 70%;
  height: 12%;
  margin-top: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-weight: 620;
  font-family: 'KaiTi';
}
.fifthtool:hover{
  cursor: pointer;
  border: 1px solid black;
  background-color: #88ff7d;
}
</style>