<template>
  <div id="bottomright" class="animate__fadeInUp animate__animated">
    <div id="rightaction" :class="{'animate__fadeInUp animate__animated':animation}">
      <div :class="{'animate__fadeInUp animate__animated':animation,'actiontool':true}" :style="{'backgroundImage':'url(/src/57.png)'}">
        <input type="file" class="addremove" :class="{'animate__fadeInUp animate__animated':animation,'actiontool':true}" @change="addmusic" v-if="!animation">
      </div>
      <div :class="{'actiontool':true,'animate__fadeInUp animate__animated':animation}" :style="{'backgroundImage':'url(/src/58.png)'}">
      </div>
      <div id="actionresearch" :class="{'animate__fadeInUp animate__animated':animation}">
        <input type="text" id="researchinput" placeholder="搜索当前库音乐，回车确定">
      </div>
    </div>

    <div id="rightlist" :class="{'animate__fadeInUp animate__animated':animation}">
      <p id="emptysay" v-if="musiclist[imgnum].length===0">
        当前没有保存有音乐
      </p>
      <li v-for="f,index in musiclist[imgnum]" :key="index" class="animate__fadeInUp animate__animated">
        <p>{{ realname(f.file.name) }}</p>
        <div id="musicsingle">
          <div class="singletool" :style="{'backgroundImage':musictool[0]}" @click="keepon(f,index)"></div>
          <div class="singletool" :style="{'backgroundImage':musictool[1]}" @click="changtoolimg(1,f)"></div>
          <div class="singletool" :style="{'backgroundImage':musictool[2]}" @click="changtoolimg(2,f)"></div>
        </div>
      </li>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus';
import 'animate.css'
import { onMounted, onUnmounted, reactive,ref } from 'vue';

export default {

  setup(){
    var nowaday=new Date()
    var imgnum=ref(0)
    var correntmusicnum=reactive([0,0,0,0,0,0,0,0])
    var correntmusicname=reactive(['','','','','','','',''])
    var animation=ref(false)
    var musicurl
    var audio
    var musiclist=reactive([
      [],
      [],
      [],
      [],
      [],
      [],
      [],
      []
    ])
    var musictool=reactive([
      'url(/src/21.png)',
      'url(/src/22.png)',
      'url(/src/24.png)'
    ])

    function onanimate(man)
    {
      animation.value=man.isit
      setTimeout(() => {
        animation.value=false
      }, 1051);
    }
    function samechange(n){
      eventBus.emit('samepagechange')
      console.log("数列：",musiclist)
      console.log("现函数：",musiclist[imgnum.value][correntmusicnum[imgnum.value]-1],correntmusicnum[imgnum.value]-1)
      if(n===0)
      {
        if(correntmusicnum[imgnum.value]===0){
          eventBus.emit("information",'这已经是第一首了！')
          return
        }else{
          keepon(musiclist[imgnum.value][correntmusicnum[imgnum.value]-1],correntmusicnum[imgnum.value]-1)
          
        }
      }
      else{
        if(correntmusicnum[imgnum.value]===musiclist.length-1){
          eventBus.emit("information",'这已经是最后一首了！')
          return
        }else{
          keepon(musiclist[imgnum.value][correntmusicnum[imgnum.value]+1],correntmusicnum[imgnum.value]+1)
        }
      }
    }

    function changtoolimg(n,f){
      if(n===1){
        if(musictool[1]==='url(/src/22.png)'){
          musictool[1]='url(/src/23.png)'
        }else{
          musictool[1]='url(/src/22.png)'
        }
        f.good=!f.good
      }else{
        if(musictool[2]==='url(/src/24.png)'){
          musictool[2]='url(/src/25.png)'
        }else{
          musictool[2]='url(/src/24.png)'
        }
        f.love=!f.love
      }
    }
    function addmusic(e){
      var file=e.target.files[0]
      musiclist[imgnum.value].push({file,chose:false,love:false,good:false})
      eventBus.emit('action',{action:"<p id=actionmain>您使用了音乐调频并添加了新歌曲<strong>《"+file.name.replace(/\.[^/.]+$/, "")+"》</strong></p>"})
      eventBus.emit('information','添加成功！')
    }
    function realname(str)
    {
      return str.replace(/\.[^/.]+$/, '')
    }
    function keepon(k,index)
    {
      console.log("原函数：",k,index)
      correntmusicnum[imgnum.value]=index
      if(k.file){
        correntmusicname[imgnum.value]=k.file.name
        musicurl=URL.createObjectURL(k.file)
        audio=new Audio(musicurl)
        audio.play()
        eventBus.emit('musicplay',audio)
      }
      eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:2,time:1,action:"<p id=actionmain>您使用了音乐调频并播放了歌曲<strong>《"+k.file.name.replace(/\.[^/.]+$/, "")+"》</strong></p>"})
      eventBus.emit('onmusic',{name:correntmusicname[imgnum.value],num:correntmusicnum[imgnum.value]})
    }
    onMounted(()=>{
      eventBus.on('changemusic',(num)=>{
        imgnum.value=num
      })
      eventBus.on('topchange',onanimate)
      eventBus.on('samechange',samechange)
    })
    onUnmounted(()=>{
      eventBus.off('changemusic',(num)=>{
        imgnum.value=num
      })
      eventBus.off('topchange',onanimate)
      eventBus.off('samechange',samechange)
    })

    return{
      musiclist,
      addmusic,
      realname,
      musictool,
      changtoolimg,
      keepon,
      imgnum,
      animation
    }
  }

}
</script>

<style scoped>
#bottomright{
  width: 40%;
  height: 85%;
}
#rightaction{
  background-color: #fff;
  width: 90%;
  height: 12%;
  margin:1% auto;
  display: flex;
  position: relative;
}
.actiontool{
  height: 95%;
  aspect-ratio: 1/1;
  margin:auto 2%;
}
.actiontool:hover{
  background-color: #bdacac9b;
  border: 1px solid black;
}
.addremove{
  width: 100%;
  height: 100%;
  opacity: 0;
}
.addremove:hover{
  cursor: pointer;
}
.actiontool:active{
  background-color: #4f4f4fcc;
}
#actionresearch{
  position: absolute;
  right:4%;
  width: 50%;
  height: 100%;
  margin:auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid black;
  border-radius: 10px;
  background-color: #ffa2a277;
}
#actionresearch:hover{
  background-color: #aeaeaeac;
}
#researchinput{
  width: 80%;
  height: 100%;
  background-color: #ffa2a277;
}
#researchinput:hover{
  background-color: #aeaeaeac;
  border: 0px solid black;
}
#rightlist{
  width: 95%;
  height: 75%;
  margin:3% auto;
  background-color: #ffffff93;
  overflow: auto;
}
#rightlist:hover{
  background-color: #e3e3e3;
}
li{
  width: 100%;
  height: 22%;
  border: 1px solid black;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
li:hover{
  background-color: #6ccbffc1;
  cursor: pointer;
}
p{
  width: 25%;
  height: 100%;
  font-size: 30px;
  font-family: 'KaiTi';
  font-weight: 650;
  display: flex;
  align-items: center;
  justify-content: center;
}
#musicsingle{
  width: 35%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  background-color: #bef3ff;

}
.singletool{
  height: 100%;
  aspect-ratio: 1/1;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
}
.singletool:hover{
  background-color: #ffffffa5;
  border: 1px solid black;
}
#emptysay{
  width: 100%;
  height: 100%;
  display: flex;
  color:rgb(223, 223, 223);
  font-size: 30px;
  background-color: #19191954;
  justify-content: center;
  align-items: center;
}
</style>