<template>
  <transition>
    <div id="main" class="animate__zoomIn animate animate_animated">
      <firstall v-if="page[0]" :alldata="props.alldata" :actionhistory="props.actionhistory"/>
      <secondmain v-if="page[1]" :alldata="props.alldata"/>
      <thirdmain v-show="page[2]" :alldata="props.alldata"/>
      <forthmain v-show="page[3]" :alldata="props.alldata"/>
      <fifthmain v-show="page[4]" :alldata="props.alldata"/>
    </div>
  </transition>
</template>

<script>
import 'animate.css'
import firstall from '../first/firstall.vue';
import secondmain from '../second/secondmain.vue';
import thirdmain from '../third/thirdmain.vue';
import forthmain from '../forth/forthmain.vue';
import fifthmain from '../fifth/fifthmain.vue';
import{ref,reactive,watch}from 'vue'
export default {
  components:{
    firstall,
    secondmain,
    thirdmain,
    forthmain,
    fifthmain
  },
  props:['pagenum','alldata','actionhistory'],
  setup(props)
  {
    console.log(props.pagenum)
    var pagebool=[true,false,false,false,false]
    var page=reactive(pagebool)
    console.log(pagebool)
    watch(()=>props.pagenum,(newv)=>{
      pagebool.fill(false)
      console.log(newv)
      page[newv]=true
    },{ immediate: true })
    return{
      page,
      props
    }
  }

}
</script>

<style scoped>
#main {
  width: 100%;
  height: 80%;
  /* border: 1px solid red; */
  position: relative; 
  transition: background-color 0.2s ease; 
  background: linear-gradient(135deg, #5050515b, #43424231, #4f51502c); /* 初始渐变背景 */
}

#main::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #7b95ffef, #ff8787dc, #31f85ccc); 
  opacity: 0; 
  transition: opacity 0.3s ease-in-out; 
  z-index: -1;
}

#main:hover::before {
  opacity: 1; 
  z-index: -1;
}


</style>