<template>
  <div class="todo-footer" v-if="todos.length">
    <label>
      <input type="checkbox" @change="selectall" :checked="isAll()">全选
    </label>
    <span>
      <span>已完成{{ donetotal() }}</span> / 全部{{ todos.length }}
    </span>
    <button class="btn btn-danger" @click="removeall">清除已完成任务</button>
  </div>
</template>

<script>
export default {
  name: "myfoot",
  props: {
    todos: { type: Array, required: true },
    removetodo: { type: Function, required: true },
    select: { type: Function, required: true }
  },
  setup(props) {
    // ✅ 已完成任务数量（方式1：forEach 统计）
    const count = () => {
      let a = 0;
      props.todos.forEach((t) => {
        if (t.done) a++;
      });
      return a;
    };

    // ✅ 已完成任务数量（方式2：reduce 统计）
    const donetotal = () => {
      return props.todos.reduce((pre, current) => {
        return pre + (current.done ? 1 : 0);
      }, 0);
    };

    // ✅ 清除已完成
    const removeall = () => {
      if (!confirm("确定清除已完成？")) return;
      for(var i=props.todos.length-1;i>=0;i--)
      {
        if(props.todos[i].done==true){
          props.removetodo(props.todos[i].id)
        }
      }
    };

    // ✅ 全选 / 取消全选
    const selectall = (event) => {
      props.select(event.target.checked);
    };

    // ✅ 是否全选
    const isAll = () => {
      const c = donetotal();
      return c === props.todos.length && props.todos.length > 0;
    };

    // 统一返回
    return {
      count,
      donetotal,
      removeall,
      selectall,
      isAll
    };
  }
};
</script>

<style scoped>
.todo-footer{
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}
.todo-footer label{
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}
.todo-footer label input{
  position: relative;
  top:-1px;
  vertical-align: middle;
  margin-right: 5px;
}
.todo-footer button {
  float:right;
  margin-top: 5px;
}
.btn{
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px rgba(255,255,255,0.2),0 1px 2px rgba(0,0,0,0.5);
  border-radius: 4px;
}
.btn-danger{
  color:#fff;
  background-color: #da4f49;
  border: 1px solid red;
}
.btn-danger:hover{
  color:#fff;
  background-color: #bd362f;
}
.btn:focus{
  outline:none;
}
</style>