<template>
  <div id="thirdchatall">
    <!-- 对话展示区 -->
    <div id="thirdchatbox">
      <div
        v-for="(msg, index) in thirdmessages"
        :key="index"
        class="thirdchatmsg"
        :class="msg.role === 'user' ? 'thirduser' : 'thirdai'"
      >
        <p class="thirdrole">{{ msg.role === 'user' ? '你' : '情绪倾诉AI' }}</p>
        <p class="thirdcontent">{{ msg.content }}</p>
        <p v-if="msg.thinking" class="thirdthinking">🤔 思考：{{ msg.thinking }}</p>
      </div>
    </div>

    <!-- 输入区 -->
    <div id="thirdinputbox">
      <input
        v-model="thirdinput"
        id="thirdinput"
        type="text"
        placeholder="请输入你的问题..."
        @keyup.enter="thirdsend"
      />
      <button id="thirdsendbtn" @click="thirdsend">发送</button>
    </div>
  </div>
</template>

<script>
import { eventBus } from "@/eventbus";
import axios from "axios";
import { ref } from "vue";

export default {
  name: "ThirdChat",
  setup() {
    const today = new Date();
    const thirdinput = ref("");
    const thirdmessages = ref([]);

    const thirdsend = async () => {
      if (!thirdinput.value.trim()) return;

      // 用户消息
      thirdmessages.value.push({
        role: "user",
        content: thirdinput.value,
      });

      const userMsg = thirdinput.value;
      thirdinput.value = "";

      // 先插入一条 AI 占位消息
      const placeholderIndex = thirdmessages.value.push({
        role: "ai",
        content: "收到您的信息，我正在思考……",
      }) - 1;

      try {
        const res = await axios.post("/api/chat", { message: userMsg });
        // 替换掉占位消息
        thirdmessages.value[placeholderIndex] = {
          role: "ai",
          content: res.data.reply,
          thinking: res.data.thinking,
        };
      } catch (err) {
        // 替换占位消息为报错信息
        thirdmessages.value[placeholderIndex] = {
          role: "ai",
          content: "出错了，请稍后重试。",
        };
        console.error(err);
      }

      eventBus.emit("changehistory", {
        year: today.getFullYear(),
        month: today.getMonth(),
        day: today.getDate(),
        where: 1,
        time: 1,
        action:"<p id=actionmain>您使用了情绪调频中的<strong>AI调频</strong></p>"
      });
    };

    return {
      thirdinput,
      thirdmessages,
      thirdsend,
    };
  },
};

</script>

<style scoped>

#thirdchatall {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 2%;
  box-sizing: border-box;
  background-color: #ffffff61;
}
#thirdchatall:hover{
  background-color: #ffd5d5d7;
}

#thirdchatbox {
  flex: 1;
  overflow-y: auto;
  border: 0.5% solid #ccc;
  padding: 2%;
  margin-bottom: 2%;
  box-sizing: border-box;
}

.thirdchatmsg {
  margin-bottom: 2%;
  width: 100%;
}

.thirduser {
  text-align: right;
}

.thirdai {
  text-align: left;
}

.thirdrole {
  font-weight: bold;
  margin-bottom: 1%;
}

.thirdcontent {
  display: inline-block;
  padding: 2% 3%;
  border-radius: 5%;
  max-width: 70%;
  word-break: break-word;
  box-sizing: border-box;
  background: #f1f1f1;
}

.thirduser .thirdcontent {
  background: #cce5ff;
}

.thirdthinking {
  font-size: 80%;
  color: #e0e0e0;
  margin-top: 1%;
  background-color: #ffffff2f;
}
.thirdthinking:hover{
  background-color: #ffffffda;
  color:black;
  box-shadow: 0 0 8px 0 rgb(175, 255, 203);
}

#thirdinputbox {
  display: flex;
  width: 100%;
  height: 8%;
}

#thirdinput {
  flex: 1;
  padding: 1%;
  border: 0.3% solid #ccc;
  border-radius: 3%;
  font-size: 100%;
  box-sizing: border-box;
}

#thirdsendbtn {
  width: 20%;
  margin-left: 2%;
  border: none;
  border-radius: 5%;
  background: #007bff;
  color: white;
  font-size: 100%;
  cursor: pointer;
}
#thirdsendbtn:hover{
  background-color: #5a35ff;
  cursor: pointer;
}
</style>
