<template>
  <transition>
    <div id="menuall" class="animate__jackInTheBox animate__animated">
      <div id="turnto"  v-if="cardanimate_show" :class="{'animate__animated animate__rotateIn':cardanimate,
        'animate__zoomOut animate__animated':isOut
      }" >
        <transition-group>
          <router-link to="/first" id="tofirst" class="goto" key="1" @click="readytopage(0)" :style="{'backgroundImage':'url(/src/48.png)'}"></router-link>
          <router-link to="/second" id="tosecond" class="goto" key="2" @click="readytopage(1)" :style="{'backgroundImage':'url(/src/49.jpg)'}"></router-link>
          <router-link to="/third" id="tothird" class="goto" key="3" @click="readytopage(2)" :style="{'backgroundImage':'url(/src/50.jpg)'}"></router-link>
          <router-link to="/forth" id="toforth" class="goto" key="4" @click="readytopage(3)" :style="{'backgroundImage':'url(/src/51.png)'}"></router-link>
          <router-link to="/fifth" id="tofifth" class="goto" key="5" @click="readytopage(4)" :style="{'backgroundImage':'url(/src/47.png)'}"></router-link>
        </transition-group>
      </div>
      <button @click="close" id="close">关闭</button>
    </div>
  </transition>
</template> 

<script>
import "animate.css"
import {ref} from 'vue'
export default {
  emits:['close','turntopage'],
  setup(props,context){
    const {emit}=context
    function close(){
      emit('close',false)
    }
    var cardanimate=ref(false)
    var isOut=ref(false)
    var cardanimate_show=ref(false)
    function cardanimation(){
      if(cardanimate.value){return}
      cardanimate.value=true
      cardanimate_show.value=true
    }
    setTimeout(() => {
      cardanimation()
    }, 500);
    function readytopage(n){
      cardanimate.value=false
      isOut.value=true
      setTimeout(() => {
        cardanimate_show.value=false
        emit('turntopage',n)
      }, 500);
      
    }
    return{
      close,
      cardanimation,
      cardanimate,
      readytopage,
      isOut,
      cardanimate_show
    }
  }
  
}
</script>

<style scoped>

#wait{
  font-size: 50px;
  background-color: #14141442;
  font-weight: 600;
  width: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25%;
  font-family: 'KaiTi';
}
#menuall{
  position:absolute;
  left:0;
  top:0;
  width: 100%;
  height: 100%;
  background-color: #686666a0;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
}
#turnto{
  width: 95%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;


}
.goto{
  width: 18%;
  height: 90%;
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  transition: transform 0.3s ease;
}
.goto:hover{
  transform: translate(0,-10%);
  background-color: #24e5ff;
  border: 3px solid black;
  cursor: pointer;
}
/* #tofirst{
  background-image: url("../../assets/48.png");
}
#tosecond{
  background-image: url("../../assets/49.jpg");
}
#tothird{
  background-image: url("../../assets/50.jpg");
}
#toforth{
  background-image: url("../../assets/51.png");
}
#tofifth{
  background-image: url("../../assets/47.png");
} */
#close{
  width: 6%;
  height: 8%;
  background-color: #fff;
  font-size: 25px;
  font-weight: 560;
  position: absolute;
  right:1%;
  top:1%
}
#close:hover{
  width: 7%;
  height: 9%;
  box-shadow: 1px 0px 1px 1px #000;
  background-color: #91ffa9;
}
#close:active{
  border: 1px solid black;
}

</style>