<template>
  <div id="fifthall">
    <fifthedit v-if="!show" :date="date" :alldata="props.alldata"/>
    <fifthshow v-if="show" :alldata="props.alldata" :oldday="oldday"/>
  </div>
</template>

<script>
import { onMounted, onUnmounted, reactive, ref } from 'vue';
import fifthedit from './fifthedit/fifthedit.vue';
import fifthshow from './fifthshow/fifthshow.vue';
import { eventBus } from '@/eventbus';
export default {
  components:{
    fifthedit,
    fifthshow
  },
  props:['alldata'],
  setup(props){
    var show=ref(true)
    var date=reactive([])
    var oldday=reactive([])
    function showoredit(man)
    {
      show.value=man.no
      date.splice(0,date.length,...man.date)
    }
    function exitedit(yes){
      show.value=yes
    }
    function saveoldday(man)
    {
      oldday[0]=man.year
      oldday[1]=man.month
      oldday[2]=man.day
    }
    onMounted(()=>{
      eventBus.on('Edition',showoredit)
      eventBus.on('exitedit',exitedit)
      eventBus.on('datechange',saveoldday)
  })
    onUnmounted(()=>{
      eventBus.off('Edition',showoredit)
      eventBus.off('exitedit',exitedit)
      eventBus.off('datechange',saveoldday)

  })

    return{
      show,
      date,
      props,
      oldday
    }
  }
}
</script>

<style scoped>
*{
  /* border: 1px solid red; */
  width: 100%;
  height: 100%;
}


</style>