<template>
  <div id="forthtop" class="animate__fadeInDown animate__animated">
    <div id="forthtopmain">
      <div class="leftright" id="left" :style="{'backgroundImage':'url(/src/56.png)'}"
      @click="changemusic(0)"
      ></div>
      <div id="forthtopmiddle">

        <img :src="imgsrc[choseimg]" alt="" id="topimg" :class="{'animate__fadeInLeft animate__animated':leftmove,
        'animate__fadeInRight animate__animated':rightmove
      }">
      <p :class="{'animate__fadeInLeft animate__animated':leftmove,
        'animate__fadeInRight animate__animated':rightmove
      }">{{ topsaying[choseimg] }}</p>

      </div>
      <div class="leftright" id="right" :style="{'backgroundImage':'url(/src/55.png)'}"
      @click="changemusic(1)"></div>
    </div>
  </div>
</template>

<script>
import 'animate.css'
import { eventBus } from '@/eventbus'
import {ref,reactive, onMounted}from 'vue'
export default {
  setup(){
    var imgsrc=reactive([
      '/src/28.jpg',
      '/src/33.jpg',
      '/src/27.jpg',
      '/src/26.jpg',
      '/src/29.jpg',
      '/src/30.jpg',
      '/src/32.jpg',
      '/src/31.jpg'
    ])
    var topsaying=reactive([
      '欢喜之刻',
      '蓝调之时',
      '狂野之间',
      '助眠脑波',
      '氛围冥想',
      '自然声音',
      '纯净音乐',
      '长时单曲',
      
    ])
    var correntplayname=reactive([
      '你好世界','你好世界','你好世界','你好世界','你好世界','你好世界','你好世界','你好世界'
    ])
    var choseimg=ref(0)
    var leftmove=ref(false)
    var rightmove=ref(false)
    function correntname(corrent)
    {
      correntplayname[choseimg.value]=corrent.name

    }
    function changemusic(direction)
    {
      
      if(direction===0)
      {
        if(choseimg.value===0)
        {
          choseimg.value=imgsrc.length-1
          
        }
        else{
          choseimg.value-=1
        }
        
        leftmove.value=false
        rightmove.value=true
      }
      else{
        if(choseimg.value===imgsrc.length-1)
        {
          choseimg.value=0
        }
        else{
          choseimg.value+=1
        }
        leftmove.value=true
        rightmove.value=false
      }
      eventBus.emit('musicimg',imgsrc[choseimg.value])
      eventBus.emit('changemusic',choseimg.value)
      setTimeout(() => {
        leftmove.value=false
        rightmove.value=false
      }, 1050);
      eventBus.emit('topchange',{isit:true,nowname:correntplayname[choseimg.value],nownum:choseimg.value})
    }
    onMounted(()=>{
      eventBus.on('onmusic',correntname)
    })


    return{
      imgsrc,
      choseimg,
      changemusic,
      leftmove,
      rightmove,
      topsaying
    }
  }
}
</script>

<style scoped>

#forthtop{
  width: 100%;
  height: 28%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff5a;
}
#forthtop:hover{
  background-color: #ffffff81;
}
#forthtopmain{
  width: 55%;
  height: 100%;
  position: relative;

}
.leftright{
  width: 7%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 50%;
  border:1px solid black;
}
.leftright:hover{
  background-color: #f8f8f883;
  width: 10%;
  cursor: pointer;
  box-shadow: 0 0 15px rgba(0, 0, 255, 0.587);
  border:2px solid black;
}
#left{
  position: absolute;
  left:0;
}
#right{
  position: absolute;
  right:0
}
#forthtopmiddle{
  position: absolute;
  left:20%;
  width: 60%;
  height: 100%;
  background-color: #ffffff58;
  display: flex;
  justify-content: space-evenly;
  font-size: 55px;
  font-weight: 550;
  align-items: center;
}
#topimg{
  height: 100%;
  aspect-ratio:1/1;
  border-radius: 12px;
}
</style>

