<template>
  <div id="editall">
    <div id="editleft" class="animate__slideInUp animate__animated">
      <div id="editlefthead">
        <div id="title">
          <div id="titlelight" :style="{'background-color':lightcolor}"></div>
          {{titletext}}
        </div>
        修改<br>{{ props.date[0] }}年{{ props.date[1]+1 }}月{{ props.date[2] }}日
        <br>情绪日志
      </div>
      <div id="editleftmain">
        <div class="maintool" @click="save">
          保存
        </div>
        <div class="maintool" @click="exit(true)">
          退出
        </div>
      </div>
    </div>

    <div id="editright" class="animate__slideInUp animate__animated">
      <div id="edittext">
        <fifthtext :alldata="props.alldata" class="animate__slideInUp animate__animated" :chosedate="props.date"/>
      </div>

      <div id="picandaudio" class="animate__slideInUp animate__animated">
        <div class="uploadmusicandaudio" :style="{'backgroundImage':'url(/src/59.png)'}">
          <input type="file" class="uploadinput" @change="inputimg">
          <p class="successupload animate__slideInUp animate__animated" v-if="picok">上传成功</p>
        </div>
        <div class="uploadmusicandaudio" :style="{'backgroundImage':'url(/src/5.png)'}">
          <input type="file" class="uploadinput" @change="inputsound">
          <p class="successupload animate__slideInUp animate__animated" v-if="soundok">上传成功</p>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus';
import { reactive, ref } from 'vue';
import fifthtext from './fifthtext.vue';
import axios from 'axios';
export default {
  props:['date','alldata'],
  components:{
    fifthtext
  },
  setup(props){
    var monthzero=(props.date[1]<10)?'0':''
    var dayzero=(props.date[2]<10)?'0':''
    var filename=JSON.stringify(`${props.date[0]}${monthzero}${props.date[1]+1}${dayzero}${props.date[2]}`)
    var lightcolor=ref('yellow')
    var titletext=ref('正在进行：')
    var imgfile=ref(null)
    var soundfile=ref(null)
    var picok=ref(false)
    var soundok=ref(false)

    function inputimg(e)
    {
      picok.value=false
      setTimeout(() => {
        picok.value=true
      }, 100);
      imgfile.value=null
      imgfile.value=e.target.files[0]
      
    }
    function inputsound(e){
      soundok.value=false
      setTimeout(() => {
        soundok.value=true
      }, 100);
      soundfile.value=null
      soundfile.value=e.target.files[0]

    }

    function stability(arr){
      var max=0
      var min=0
      var secondmax=0
      var secondmin=0
      for(var i=0;i<arr.length;i++){
        if(arr[i]<min){
          min=arr[i]
        }
        if(arr[i]>max){
          max=arr[i]
        }
        if(arr[i]<max&&arr[i]>secondmax){
          secondmax=arr[i]
        }
        if(arr[i]>min&&arr[i]<secondmin){
          secondmin=arr[i]
        }
      }
      var re=max+secondmin-secondmax+min
      console.log("稳定度的结果",max,min,secondmax,secondmin,re)
      return re
    }

    function save()
    {
      lightcolor.value='orange'
      titletext.value='正在保存...'
      if(imgfile.value===null&&soundfile.value===null&&props.alldata[props.date[0]][props.date[1]][props.date[2]].img===''&&props.alldata[props.date[0]][props.date[1]][props.date[2]].sound===''){
        if(confirm("您还有图片或音频可以上传，以完善今天的情绪分析，确定退出吗？")){
        }else{
          return
        }
      }
      const formData=new FormData()
      if(imgfile.value){
        formData.append('pic',imgfile.value)
      }
      if(soundfile.value){
        formData.append('sound',soundfile.value)
      }
      if (props.date) {
        formData.append('date', filename);
      }
      if(imgfile.value!==null||soundfile.value!==null){
        eventBus.emit("savethis",true)
        var text=props.alldata[props.date[0]][props.date[1]][props.date[2]].diary
        formData.append('text',text)
        // console.log("全部数据",props.alldata)
        // console.log("年月日：",props.date[0],props.date[1],props.date[2])
        // alert(text)
        axios.post('/api/daily', formData,  {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }).then(res=>{
          lightcolor.value='green'
          titletext.value='保存成功！'
          console.log("新数据",res.data.result)
          props.alldata[props.date[0]][props.date[1]][props.date[2]].mood=res.data.result
          if(res.data.saved_files.pic)props.alldata[props.date[0]][props.date[1]][props.date[2]].img=res.data.saved_files.pic
          if(res.data.saved_files.sound)props.alldata[props.date[0]][props.date[1]][props.date[2]].sound=res.data.saved_files.sound
          eventBus.emit("wehavedata",res.data.result)
          eventBus.emit('changehistory',{year:props.date[0],month:props.date[1],day:props.date[2],where:3,time:1,
            action:`<p id=actionmain>您成功编辑了${props.date[0]}年${props.date[1]+1}月${props.date[2]}日的<strong>情绪日志</strong></p>`
          })
          eventBus.emit('information','保存成功！')
          eventBus.emit('changeindicator',{year:props.date[0],month:props.date[1],day:props.date[2],firstvalue:res.data.result[0]+res.data.result[1]+res.data.result[4],secondvalue:res.data.result[1]+res.data.result[2]+res.data.result[4]+res.data.result[5],
            thirdvalue:100-res.data.result[6],forthvalue:stability(res.data.result)
          })
          exit(false)
          
        }).catch(err=>{
          eventBus.emit('information','服务端出错，请稍后再试！')
          lightcolor.value='red'
          titletext.value='保存出错！...'
          console.log(err)
          eventBus.emit('action',{action:`<p id=actionmain>您编辑${props.date[0]}年${props.date[1]+1}月${props.date[2]}日的<strong>情绪日志</strong>时出错了</p>`})
          
        })
        
      }
      else{
        eventBus.emit("savethis",true)
        lightcolor.value='orange'
        titletext.value='正在保存...'
        var text=props.alldata[props.date[0]][props.date[1]][props.date[2]].diary
        formData.append('text',text)
        axios.post('/api/daily', formData,  {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }).then(res=>{
          eventBus.emit("savethis",true)
          lightcolor.value='green'
          titletext.value='保存成功！'
          eventBus.emit('information','保存成功！')
          console.log("新数据",res.data.result)
          props.alldata[props.date[0]][props.date[1]][props.date[2]].mood=res.data.result
          eventBus.emit("wehavedata",res.data.result)
          exit(false)
          eventBus.emit('changeindicator',{year:props.date[0],month:props.date[1],day:props.date[2],firstvalue:res.data.result[0]+res.data.result[1]+res.data.result[4],secondvalue:res.data.result[1]+res.data.result[2]+res.data.result[4]+res.data.result[5],
            thirdvalue:100-res.data.result[6],forthvalue:stability(res.data.result)
          })
          eventBus.emit('changehistory',{year:props.date[0],month:props.date[1],day:props.date[2],where:3,time:1,
            action:`<p id=actionmain>您成功编辑了${props.date[0]}年${props.date[1]+1}月${props.date[2]}日的<strong>情绪日志</strong></p>`
          })
          
        }).catch(err=>{
          console.log(err)
          eventBus.emit('action',{action:`<p id=actionmain>您编辑${props.date[0]}年${props.date[1]+1}月${props.date[2]}日的<strong>情绪日志</strong>时出错了</p>`})
          eventBus.emit('information','服务端出错，请稍后再试！')
        })
        
        
      }
    }
    function exit(whether)
    {
      if(whether){
        if(confirm("当前页面的数据将不会保存，确认退出？")){
          lightcolor.value='grey'
          titletext.value='正在退出...'
          setTimeout(() => {
            eventBus.emit('exitedit',true)
          }, 1000);
        }
      }else{
          lightcolor.value='grey'
          titletext.value='正在退出...'
          setTimeout(() => {
            eventBus.emit('exitedit',true)
          }, 1000);
      }
    }
    return{
      props,
      lightcolor,
      titletext,
      picok,
      soundok,
      save,
      exit,
      inputimg,
      inputsound
    }
  }
}
</script>

<style scoped>
 
#editall{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#editleft{
  width: 25%;
  height: 75%;
  background-color: #ffffff98;
}
#editlefthead{
  width: 80%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 580;
  margin:5% auto;
  position: relative;
}
#title{
  position: absolute;
  left:0;
  top:0;
  width: 45%;
  height: 30%;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#titlelight{
  height: 40%;
  aspect-ratio: 1/1;
  border-radius: 50%;
}
#editleftmain{
  width: 80%;
  height: 50%;
  margin: 2% auto;
  display: flex;
  justify-content: center;
  justify-content: space-evenly;
}
.maintool{
  width: 40%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 625;
  font-family: 'KaiTi';
}
.maintool:hover{
  background-color: #dadadab9;
  cursor: pointer;
}

#editright{
  width: 65%;
  height: 85%;
  background-color: #ffffff65;
}
#edittext{
  width: 100%;
  height: 75%;
}
#picandaudio{
  width: 90%;
  margin:0 auto;
  height: 25%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.uploadmusicandaudio{
  width: 40%;
  height: 95%;
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transition:background-color 0.1s ease,border 0.1s ease;
}
.uploadmusicandaudio:hover{
  background-color: #3434346d;
  border: 1px dashed grey;
  box-shadow: 0 0  10px 0 rgba(22, 255, 232, 0.758);
}
.uploadinput{
  width: 100%;
  height: 100%;
  opacity: 0;
  position: relative;
}
.successupload{
  position: absolute;
  left:20%;
  top:30%;
  width: 60%;
  height: 40%;
  font-size: 30px;
  font-weight: 620;
  color:#11ff94;
  background-color: #61c8ff8e;
  box-shadow: 0 0 12px 0 rgb(2, 255, 18);
  display: flex;
  justify-content: center;
  align-items: center;
}

</style>