<template>
  <div class="todo-header">
    <input type="text" v-model="inner" placeholder='按下回车确认；明天会清除今天的所有事项' @keyup.enter="add">
  </div>
</template>

<script>
import {nanoid} from 'nanoid'
import {ref,reactive}from "vue"
export default {
  name:'top',
  props:{
    receive:Function
  },
  setup(props){
    var inner=ref("")
    function add(e){
      console.log(inner.value)
      if(inner.value.trim()==="")
      {
        eventBus.emit('information','请输入内容！')
        return;
      }
      console.log("接收请求发送")
      var content={id:nanoid(),title:e.target.value,done:false,isedit:false}
      props.receive(content)
      // this.$emit("addtodo",e.target.value)
      inner.value=""
    }
    return {
      inner,
      add
    }
  }
}
</script>

<style scoped>
.todo-header input{
  width: 100%;
  font-size: 24px;
  border: 1px solid #Ccc;
  border-radius: 4px;
  padding: 4px 7px;
  margin-bottom: 1%;
}
.todo-header input:focus{
  outline: none;
  border-color: rgba(82,168,236,0.8);
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.075),0 0 8px rgba(82,168,236,0.8);
}
</style>