<template>
  <div id="secondbrainre">
    <canvas ref="chartCanvas"></canvas>
    <br><hr>
    <div id="secondpicsay" v-if="resultText">
      识别的结果为：
      <h1>{{ resultText }}</h1>
    </div>
  </div>
</template>

<script>
import { ref, nextTick, onMounted, onUnmounted } from 'vue';
import { Chart } from 'chart.js/auto'
import { eventBus } from '@/eventbus';
export default {
  props: ['result'],
  setup(props) {
    function mainmood(ans) {
      let mainre = "";
      let mainrenum = 0;
      ans.forEach((el, index) => {
        if (el > mainrenum) {
          mainre = labels[index];
          mainrenum = el;
        }
      });
      return mainre;
    }
    const nowaday=new Date()
    const braindata = Object.values(props.result.data)
      console.log("***",braindata)


      braindata.forEach((el, index) => {
        braindata[index] = Number(braindata[index]);
      });
    
    const labels = ["消极", "中性", "积极"];
    const resultText = ref('');
    resultText.value = mainmood(braindata);
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:`<p class=actionmain>您使用了脑电识别，识别结果是<strong>${resultText.value}</strong></p>`})

    const chartCanvas = ref(null);
    let chartInstance = null;
    

    async function showtextre(res) {
      await nextTick();

      if (!chartCanvas.value) {
        console.error('canvas 还没挂载');
        return;
      }

      if (chartInstance) {
        chartInstance.destroy();
      }


      

      const ctx = chartCanvas.value.getContext('2d');


      chartInstance = new Chart(ctx, {
        type: 'bar', 
        data: {
          labels, 
          datasets: [{
            label: '情绪占比 (%)', 
            data: braindata, 
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)', 
              'rgba(255, 206, 86, 0.2)', 
              'rgba(75, 192, 192, 0.2)'  
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)', 
              'rgba(255, 206, 86, 1)', 
              'rgba(75, 192, 192, 1)'  
            ],
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true, 
              max: 100, 
              ticks: {
                callback: function (value) {
                  return value.toFixed(0) + '%'; 
                }
              }
            }
          },
          plugins: {
            legend: {
              display: true 
            }
          }
        }
      });
      
      // eventBus.emit('actionhistory',{ation:`<p class=actionmain>您刚刚使用了音频识别，识别结果是<strong>${resultText.value}</strong></p>`})
    }

    onMounted(() => {
      showtextre(props.result);
    });

    onUnmounted(() => {
      if (chartInstance) {
        chartInstance.destroy();
      }
    });

    return {
      resultText,
      chartCanvas
    };
  }
};
</script>

<style scoped>
#secondbrainre{
  width: 100%;
  height: 100%;
  background-color: #ffffff82;
}
#secondbrainre:hover{
  background-color: #ffffffc6;
}
#secondpicsay{
  width: 90%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-family: 'KaiTi';
  margin:0 auto;
}
</style>