<template>
  <div id="firstrighttop">
    <div class="topchoose" :class="{'isactive':choice[0]}" @click="changechoice(0)">
      历史总绪
    </div>
    <div  class="topchoose" :class="{'isactive':choice[1]}" @click="changechoice(1)">
      近期总绪
    </div>
    <div  class="topchoose" :class="{'isactive':choice[2]}" @click="changechoice(2)">
      今日回顾
    </div>
  </div>
</template>

<script>
import{ref,reactive}from 'vue'
export default {
  setup(props,{emit}){
    var choice=reactive([true,false,false])
    function changechoice(n){
      choice.forEach((el,index)=>{
        choice[index]=false
      })
      choice[n]=true
      emit('firstchangechoice',n)
    }

    return{
      changechoice,
      choice
    }
  }
}
</script>

<style scoped>
 
#firstrighttop{
  width: 40%;
  height: 10%;
  transform: translate(0,-100%);
  display: flex;
  z-index: 200;
}
.topchoose{
  width: 32%;
  height: 100%;
  margin-right: 1%;
  border-radius: 0 50% 0 0;
  font-family: 'KaiTi';
  font-size: 22px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  transition: transform 0.2s ease;
  text-decoration: none;
  background-color: #b3ffac;
  color:rgb(51, 0, 255);
  z-index: 200;
}
.topchoose:hover{
  background-color: #a1ffe7;
  cursor: pointer;
  box-shadow: 0 0 10px 0 black;
  transform: translate(0,20%);
  color:rgb(181, 10, 244)
}
.topchoose:active{
  background-color: #98f9c1;
  box-shadow: inset 0 0 5px 0 black;
}
.isactive{
  background-color: #34fecc;
  box-shadow: 0 0 10px 0 black;
  transform: translate(0,20%);
}
</style>