<template>
  <div id="thirdall" >
    <div id="return" v-if="!enter" @click="comeback"
    :class="[
      chooseleft?'leftreturn':'',
      chooseright?'rightreturn':'',
      !enter&&chooseleft ? 'animate__fadeInLeft animate__animated' : '',
      !enter&&chooseright ? 'animate__fadeInRight animate__animated' : '',
      enter?'animate__fadeOut':''
    ]">
      返回
    </div>
    <!-- left -->
    <div :class="[
      !chooseleft ? 'both':'',
      enter ? 'animate__fadeInLeft animate__animated' : '',
      leftout ? 'animate__fadeOutLeft animate__animated' : '',
      chooseleft ? 'bechosen' :''
    ]" >
      <thirdleft/>
    </div>
    
    <!-- middle -->
    <div id="chosediv" v-if="!middleout"
      :class="[
        enter ? 'animate__fadeInUp animate__animated' : '',
        middleout ? 'animate__fadeOutDown animate__animated' : ''
      ]">
      <div class="chose" style="background-image: url('./src/60.png');" @click="choseleft" v-if="!middleout"></div>
      <div class="chose" style="background-image: url('./src/61.png');" @click="choseright" v-if="!middleout"></div>
    </div>

    <!-- right -->
    <div :class="[
      !chooseright ? 'both':'',
      enter ? 'animate__fadeInRight animate__animated' : '',
      rightout ? 'animate__fadeOutRight animate__animated' : '',
      chooseright ? 'bechosen' :''
    ]" >
    <thirdright/>
    </div>
  </div>
</template>


<script>
import { ref } from 'vue'
import thirdleft from './thirdleft/thirdleft.vue'
import thirdright from './thirdright/thirdright.vue' 
import 'animate.css'
import { eventBus } from '@/eventbus'

export default {
  components: {
    thirdleft,
    thirdright
  },
  setup() {
    const leftout = ref(false)
    const enter = ref(true)
    const middleout = ref(false)
    const rightout = ref(false)
    const chooseleft=ref(false)
    const chooseright=ref(false)
    function choseleft()
    {
      enter.value=false
      middleout.value=true
      chooseleft.value=true
      rightout.value=true
      chooseright.value=false
      leftout.value=false
      eventBus.emit('information','选择：AI情绪缓解')
    }
    function choseright()
    {
      enter.value=false
      middleout.value=true
      chooseright.value=true
      chooseleft.value=false
      leftout.value=true
      rightout.value=false
      eventBus.emit('information','选择：情绪舒缓助手')
    }
    function comeback()
    {
      leftout.value = false
      enter.value = true
      middleout.value = false
      rightout.value = false
      chooseleft.value=false
      chooseright.value=false
    }

    return {
      leftout,
      enter,
      middleout,
      rightout,
      chooseleft,
      chooseright,
      
      choseright,
      choseleft,
      comeback
    }
  }
}
</script>

<style scoped>

.both{
  width: 32%;
  height: 100%;
}
#thirdall{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
#chosediv{
  width: 25%;
  display: flex;
  height: 20%;
  justify-content: space-evenly;;
}
.chose{
  width: 65%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  }
.chose:hover{
  background-color: #ffffffa7;
  width: 50%;
  height: 100%;
  cursor: pointer;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.539);
}
.bechosen{
  width: 90%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff21;
}
#return{
  width: 15%;
  height: 80%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  font-weight: 600;
  flex-direction: column;
  font-family: 'KaiTi';
  background-color: #ffebebad;

}
#return:hover{
  background-color: #1b1b1ba0;
  color:white;
  cursor: pointer;
}
.rightreturn{
  left:5%;
  top:10%  
}
.leftreturn{
  right:5%;
  top:10%;
}
</style>