<template>
  <div id="secondresult">
    <canvas ref="chartCanvas"></canvas>
    <br><hr>
    <div id="secondpicsay" v-if="resultText">
      识别的结果为：
      <h1>{{ resultText }}</h1>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus'
import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import { Chart } from 'chart.js/auto'

export default {
  props:['result'],
  setup(props) {
    const nowaday=new Date()
    console.log(props.result)
    const picdatachange={
      '高兴':0,
      '伤心':1,
      '愤怒':2,
      '惊讶':3,
      '厌恶':4,
      '恐惧':5,
      '中性':6
    }
    const resultText = ref('')
    console.log("结果",props.result.data.re)
    resultText.value = props.result.data.re
    console.log('图片这边的表格',props.result.data)
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:`<p class=actionmain>您刚刚使用了图片识别，识别结果是<strong>${resultText.value}</strong></p>`})
    eventBus.emit('changemoodhistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:picdatachange[props.result.data.re],time:1})
    eventBus.emit('actionhistory',{action:`<p class=actionmain>您使用了图片识别，识别结果是<strong>${resultText.value}</strong></p>`})
    
    const chartCanvas = ref(null)
    let chartInstance = null
    

    

    const labels = ["愤怒", "厌恶", "恐惧", "快乐", "复杂", "悲伤", "惊讶"]
    async function showpicre(res) {
      await nextTick()

      if (!chartCanvas.value) {
        console.error('canvas 还没挂载')
        return
      }

      // 销毁旧图表，防止重复
      if (chartInstance) {
        chartInstance.destroy()
      }

      const picdata = [
        res.data.angry,
        res.data.disgust,
        res.data.fear,
        res.data.happy,
        res.data.neutral,
        res.data.sad,
        res.data.surprise
      ]

      const ctx = chartCanvas.value.getContext('2d')
      chartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels,
          datasets: [{
            label: '情感分析结果',
            data: picdata,
            backgroundColor: [
              'rgba(255, 99, 132)',
              'rgba(54, 162, 235)',
              'rgba(255, 206, 86)',
              'rgba(75, 192, 192)',
              'rgba(153, 102, 255)',
              'rgba(255, 159, 64)',
              'rgba(255, 255, 255)'
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(255, 255, 255, 1)'
            ],
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              ticks: {
                callback: function (value) {
                  return value.toFixed(0) + '%'
                }
              }
            }
          }
        }
      })
      
      
    }
    showpicre(props.result)

    onMounted(() => {
      
    })

    onUnmounted(() => {
      if (chartInstance) {
        chartInstance.destroy()
      }
    })

    return {
      resultText,
      chartCanvas
    }
  }
}
</script>

<style scoped>

#secondresult{
  width: 100%;
  height: 100%;
  background-color: #ffffff82;
}
#secondresult:hover{
  background-color: #ffffffc6;
}
#secondpicsay{
  width: 80%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-family: 'KaiTi';
  margin:0 auto;
}
</style>